import React, { Component } from 'react';
import PropTypes from 'prop-types';
import DeleteIcon from 'material-ui-icons/Delete';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';
import auth from '../../auth/auth-helper';
import { remove } from './api-lease.js';
import { withTranslation } from 'react-i18next';
import { BitOlxButton, BitOlxIconButton } from '../../commons';

class DeleteLease extends Component {
  state = {
    open: false,
  };
  clickButton = () => {
    this.setState({ open: true });
  };
  deleteLease = () => {
    console.log(this.props.lease);
    const jwt = auth.isAuthenticated();
    remove(this.props.lease._id,
      { t: jwt.token }
    ).then(data => {
      if (data.error) {
        console.log(data.error);
      } else {
        this.setState({ open: false }, () => {
          this.props.onRemove(this.props.lease);
        });
      }
    });
  };
  handleRequestClose = () => {
    this.setState({ open: false });
  };
  render() {
    const { t } = this.props;
    return (
      <span>
        <BitOlxIconButton icon={<DeleteIcon />} onClick={this.clickButton} />
        <Dialog open={this.state.open} onClose={this.handleRequestClose}>
          <DialogTitle>
            {'?'}
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              {'  '}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <BitOlxButton
              title={t('Cancel')}
              onClick={this.handleRequestClose}
              fullWidth
            />
            <BitOlxButton
              fullWidth
              title={''}
              onClick={this.deleteLease}
            />
          </DialogActions>
        </Dialog>
      </span>
    );
  }
}
DeleteLease.propTypes = {
  lease: PropTypes.object.isRequired,
  onRemove: PropTypes.func.isRequired,
};
export default withTranslation()(DeleteLease);
