import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
// import Button from 'material-ui/Button';
import Card, { CardMedia } from 'material-ui/Card';
import Edit from 'material-ui-icons/Edit';
import Message from 'material-ui-icons/Forum';
import Info from 'material-ui-icons/Info';
import List, { ListItem, ListItemSecondaryAction } from 'material-ui/List';
import { Link } from 'react-router-dom';
import Divider from 'material-ui/Divider';
import InfiniteScroll from 'react-infinite-scroller';
import auth from '../../auth/auth-helper';
import { listByOwner } from './api-product.js';
import placeholderImage from '../../assets/img/placeholder.jpg';
import DeleteProduct from './DeleteProduct';
import { read } from '../user/api-user.js';
import config from '../../config.json';
import { withTranslation } from 'react-i18next';
import { BitOlxButton, BitOlxIconButton } from '../../commons';
import ReactTooltip from "react-tooltip";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import MyLeases from './MyLeases';
import 'react-tabs/style/react-tabs.scss';
import './LordProducts.scss';

const styles = theme => ({
  leftIcon: {
    marginRight: '8px',
  },
  subheading: {
    marginTop: theme.spacing.unit * 2,
    color: theme.palette.openTitle,
  },
  cover: {
    width: 110,
    height: 100,
    margin: '8px',
    borderRadius: '5px',
  },
  details: {
    padding: '10px',
  },
  actionBtn: {
    display: 'flex',
  },
  editBtn: {
    marginRight: '4px',
  }
});

class MyProducts extends Component {
  constructor({ match }) {
    super();
    this.state = {
      products: [],
      spreadsheetUrl: '',
      spreadsheetId: '',
      count: 1,
      page: 0,
      hideModal: false,
      error: '',
    };
    this.match = match;
  }

  numberWithCommas(x, currency) {
    let number = x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    if (currency == '') {
      return `${number} `
    }
    else {
      return `$${number}`
    }
  }

  componentDidMount = () => {
    const jwt = auth.isAuthenticated();
    read(
      {
        userId: auth.isAuthenticated().user._id,
      },
      { t: jwt.token }
    ).then(data => {
      if (data.error) {
        this.setState({ error: data.error });
      } else {
        this.loadProducts();
        this.setState({
          spreadsheetUrl: `https://docs.google.com/spreadsheets/d/${data.spreadsheetId}`,
          spreadsheetId: data.spreadsheetId,
          isVerified: data.isVerified
        });
      }
    });
  };
  loadProducts() {
    const jwt = auth.isAuthenticated();
    listByOwner(jwt.user._id, this.state.page).then(data => {
      if (data && data.error) {
        console.log(data.error);
        setTimeout(() => {
          console.log('Err #', data.error);
          this.loadProducts();
        }, 1000);
      } else {
        if (data) {
          let newProducts = data.products;
          let veryNewProducts = newProducts.map((product, index) => {
            return this.productJSXTemplate(product, index);
          });
          this.setState({
            products: [...veryNewProducts],
            page: this.state.page + 1,
            count: data.count,
          });
        }
      }
    });
  }
  
  removeProduct = product => {
    const updatedProducts = this.state.products;
    const index = updatedProducts.indexOf(product);
    updatedProducts.splice(index, 1);
    this.setState({ shops: updatedProducts });
  };

  productJSXTemplate(product, i) {
    const { classes } = this.props;
    const { t } = this.props;
    return (
      <span key={i}>
        <ListItem>
          <Link to={'/apartment/' + product.name + '/' + product._id}>
            <img
              className={classes.cover}
              src={product.images[0]}
              onError={(e) => { e.target.onerror = null; e.target.src = placeholderImage }}
              title={product.name}
            />
          </Link>

          <div className={classes.details}>
            <div
              type="headline"
              component="h2"
              color="primary"
              className={classes.productTitle}
            >
              {product.name}
            </div>
            <div
              type="subheading"
              component="h4"
              className={classes.subheading}
            >
              {t('Price')}: {this.numberWithCommas(product.price, product.currency)}
            </div>
            {!product.isApproved && (
              <div style={{ fontSize: '13px' }}>
                {t('CITVY AI  ...')}
              </div>
            )}
          </div>

          <ListItemSecondaryAction className={classes.actionBtn}>
            <Link
              to={'/seller/' + product.owner._id + '/' + product._id + '/edit'}
              className={classes.editBtn}
            >
              <BitOlxIconButton icon={<Edit />} />
            </Link>
            <DeleteProduct
              product={product}
              ownerId={this.props.ownerId}
              onRemove={this.removeProduct}
            />
          </ListItemSecondaryAction>
        </ListItem>
        <Divider />
      </span>
    );
  }
  render() {
    const { t } = this.props;
    return (
      <Tabs>
        {!this.state.hideModal && <div>
          {this.state.isVerified == 'pending' ?
            <div className="verificationBox"><div style={{ fontSize: '22px', fontWeight: 'bold' }}>  </div><div style={{ marginBottom: '10px' }}>     24- </div><div style={{ display: 'flex' }}>         <BitOlxButton onClick={() => {
              this.setState({ hideModal: true })
            }} title={''} /></div></div> : this.state.isVerified == 'verified' ? null : <div className="verificationBox"><div style={{ fontSize: '22px', fontWeight: 'bold' }}> </div><div style={{ marginBottom: '10px' }}>    1 </div><div style={{ display: 'flex' }}>            <a style={{ marginRight: '10px' }} href='/kyc'
            ><BitOlxButton title={''} /></a><BitOlxButton onClick={() => {
              this.setState({ hideModal: true })
            }} title={''} /></div></div>}
        </div>}
        <TabList>
          <Tab> </Tab>
          <Tab> </Tab>
        </TabList>
        <TabPanel>
          <div>
            {this.state.products.length > 0 ? (
              <List dense>
                <ReactTooltip place="bottom" id="infoicon" type="info">
                  <span>          </span>
                </ReactTooltip>
                <div style={{ display: 'flex', justifyContent: 'space-between' }}><div style={{ marginLeft: '10px', fontSize: '1rem' }}><b> :</b></div><Info data-tip data-for="infoicon" style={{ marginRight: '10px' }} /></div>
                <Divider />
                <InfiniteScroll
                  loadMore={this.loadProducts.bind(this)}
                  hasMore={
                    this.state.products.length >= this.state.count ? false : true
                  }
                  threshold={200}
                  loader={
                    <div className="loader" key={0}>
                      ...
                </div>
                  }
                >
                  {this.state.products}
                </InfiniteScroll>
              </List>
            ) : (
                <div>{t('You dont have any products yet')}</div>
              )}
            <div className="add-buttons-box">
              <Link to={'/products/new'} className="add-button">
                <BitOlxButton fullWidth title={t('New Product')} />
              </Link>
              {this.state.spreadsheetId ? (
                <a
                  style={{ textDecoration: 'none' }}
                  href={this.state.spreadsheetUrl}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="add-button"
                >
                  <BitOlxButton fullWidth title={t('Switch to Google Sheets')} />
                </a>
              ) : (
                  <a
                    style={{ textDecoration: 'none' }}
                    href={`/user/edit/${auth.isAuthenticated().user._id}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="add-button"
                  >
                    <BitOlxButton fullWidth title={t('Add Spreadsheet URL')} />
                  </a>
                )}
              {this.state.spreadsheetId && (
                <BitOlxButton
                  title={t('Refresh products')}
                  fullWidth
                  onClick={() => {
                    fetch(
                      `${config.apiURL}/bitolx/api/users/spreadsheet?spreadsheetId=${this.state.spreadsheetId}`
                    ).then(() => {
                      window.location.reload();
                    });
                  }}
                />
              )}
            </div>
          </div>
        </TabPanel>
        <TabPanel>
          <MyLeases />
        </TabPanel>
      </Tabs>
    );
  }
}
MyProducts.propTypes = {
  classes: PropTypes.object.isRequired,
};

const NamespaceComponent = withTranslation()(MyProducts);
export default withStyles(styles)(NamespaceComponent);
