import React, { Component } from 'react';
import { Document, Page } from 'react-pdf/dist/entry.webpack';
import { BitOlxButton, BitOlxIconButton } from '../../commons';
import Dropzone from "react-dropzone";
import request from "superagent";
import auth from '../../auth/auth-helper';
import { listByOwner, create, update } from './api-lease.js';
import { IconButton } from '@material-ui/core';
import { GetApp, Delete } from '@material-ui/icons';
import DeleteLease from './DeleteLease';
import './Leases.scss';

class MyLease extends Component {
    state = {
        file: this.props.lease,
        numPages: null,
        pageNumber: 1
    }

    onDocumentLoadSuccess = ({ numPages }) => {
        this.setState({ numPages, pageNumber: 1 });
    }

    previousPage() {
        this.setState({ pageNumber: this.state.pageNumber - 1 });
    }

    nextPage() {
        this.setState({ pageNumber: this.state.pageNumber + 1 });
    }

    render() {
        const { file, numPages, pageNumber } = this.state;

        return (
            <div className="pdf-doc">
                <div>
                    <Document
                        file={file}
                        onLoadSuccess={this.onDocumentLoadSuccess.bind(this)}
                    >
                        <Page className="lease" pageNumber={pageNumber} />
                    </Document>

                    <a style={{ margin: '15px', bottom: 0, position: 'absolute' }} href={file.url} target="_blank">
                        <BitOlxIconButton icon={<GetApp />} />
                    </a>
                    <div className="action-lease-btn">
                        <DeleteLease style={{ marginTop: '10px', position: 'absolute', marginLeft: '210px' }} lease={file}
                            ownerId={this.props.ownerId}
                            onRemove={this.props.removeLease} />
                    </div>

                    <div className="page-controls">

                        <button
                            type="button"
                            disabled={pageNumber <= 1}
                            onClick={this.previousPage.bind(this)}
                        >
                            {'<'}
                        </button>
                        <span>{pageNumber || (numPages ? 1 : '--')} of {numPages || '--'}</span>
                        <button
                            type="button"
                            disabled={pageNumber >= numPages}
                            onClick={this.nextPage.bind(this)}
                        >
                            {'>'}
                        </button>

                    </div>

                </div>
            </div>
        );
    }
}

export default class MyLeases extends Component {
    state = {
        file: { url: this.props.url },
        numPages: null,
        pageNumber: 1,
        leases: []
    }

    componentDidMount = () => {
        const jwt = auth.isAuthenticated();
        listByOwner(jwt.user._id, { t: jwt.token }).then(data => {
            if (data.error) {
                this.setState({ error: data.error });
            } else {
                this.setState({
                    leases: data.leases
                });
            }
        });
    };

    removeLease = lease => {
        const updatedLeases = this.state.leases;
        const index = updatedLeases.indexOf(lease);
        updatedLeases.splice(index, 1);
        this.setState({ shops: updatedLeases });
    };

    onLease(file) {
        let percent = {};
        const jwt = auth.isAuthenticated();
        this.percent = {};
        this.notLoadedAvatar = false;
        const cloudName = "bitolx", uploadPreset = "yh7pqbfm";
        const url = `https://api.cloudinary.com/v1_1/${
            cloudName
            }/upload`;
        request.post(url)
            .field('upload_preset', uploadPreset)
            .field('file', file)
            .on('progress', (progress) => {
                percent[file.name] = progress.percent;
                this.percent = percent;
            })
            .end((error, response) => {
                try {
                    if (response.status === 200) {
                        let lease = {
                            url: response.body.url,
                            tenantSigned: false,
                            landlordSigned: false
                        };
                        create(jwt.user._id, { t: jwt.token }, lease).then(
                            data => {
                                let updatedLeases = this.state.leases;
                                updatedLeases.push(data);
                                this.setState({ leases: updatedLeases })
                            }
                        );
                    }
                    else if (error) {
                        console.log("Error", error);
                    }
                }
                catch (err) {
                    console.log(err);
                }
            });
    }

    render() {
        const renderedLeases = [];

        for (const [index, value] of this.state.leases.entries()) {
            renderedLeases.push(<MyLease key={index} lease={value} removeLease={this.removeLease.bind(this)} />)
        }
        return (
            <div>
                <div className='lease-docs'>
                    {renderedLeases}
                </div>
                <div className="add-buttons-box">
                    <Dropzone
                        style={{ borderStyle: "hidden" }}
                        onDrop={this.onLease.bind(this)}
                    >
                        <BitOlxButton title={' '} />
                    </Dropzone>
                    <br />&nbsp;<br />
                </div>
            </div>
        );
    }
}
