const config = require('../../config.json');

const create = user => {
  return fetch(`${config.apiURL}/bitolx/api/users`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(user),
  })
    .then(response => {
      return response.json();
    })
    .catch(err => console.log(err));
};

const list = () => {
  return fetch(`${config.apiURL}/bitolx/api/users/`, {
    method: 'GET',
  })
    .then(response => {
      return response.json();
    })
    .catch(err => console.log(err));
};

const read = params => {
  return fetch(`${config.apiURL}/bitolx/api/users/${params.userId}`, {
    method: 'GET',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
  })
    .then(response => {
      return response.json();
    })
    .catch(err => console.log(err));
};

const update = (params, credentials, user) => {
  return fetch(`${config.apiURL}/bitolx/api/users/${params.userId}`, {
    method: 'PUT',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + credentials.t,
    },
    body: JSON.stringify(user),
  })
    .then(response => {
      return response.json();
    })
    .catch(err => console.log(err));
};

const remove = (params, credentials) => {
  return fetch(`${config.apiURL}/bitolx/api/users/${params.userId}`, {
    method: 'DELETE',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + credentials.t,
    },
  })
    .then(response => {
      return response.json();
    })
    .catch(err => console.log(err));
};

const stripeUpdate = (params, credentials, auth_code) => {
  return fetch(`${config.apiURL}/bitolx/api/stripe_auth/${params.userId}`, {
    method: 'PUT',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + credentials.t,
    },
    body: JSON.stringify({ stripe: auth_code }),
  })
    .then(response => {
      return response.json();
    })
    .catch(err => {
      console.log(err);
    });
};

export { create, list, read, update, remove, stripeUpdate };
