import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Divider from 'material-ui/Divider';
import { makeStyles } from '@material-ui/core/styles';
import cart from './cart-helper.js';
import { BitOlxButton } from '../../commons';
import { withTranslation } from 'react-i18next';
import placeholderImage from '../../assets/img/placeholder.jpg';
import transliterate from 'transliterate';

const useStyles = makeStyles((theme) => ({
  card: {
    margin: '24px auto',
    paddingTop: '16px',
    maxWidth: '700px',
  },
  title: {
    margin: theme.spacing.unit * 2,
    color: theme.palette.openTitle,
    fontSize: '1.2em',
  },
  price: {
    color: theme.palette.text.secondary,
    display: 'inline',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    marginTop: 0,
    width: 50,
  },
  productTitle: {
    fontSize: '1.15em',
    marginBottom: '5px',
  },
  subheading: {
    color: 'rgba(88, 114, 128, 0.67)',
    padding: '8px 10px 0',
    cursor: 'pointer',
    display: 'inline-block',
  },
  cart: {
    width: '100%',
    display: 'inline-flex',
  },
  details: {
    display: 'inline-block',
    width: '100%',
    padding: '4px',
  },
  content: {
    flex: '1 0 auto',
    padding: '16px 8px 0px',
  },
  cover: {
    width: 160,
    height: 125,
    margin: '8px',
  },
  itemTotal: {
    float: 'right',
    marginRight: '40px',
    fontSize: '1.2em',
    color: 'rgb(72, 175, 148)',
  },
  checkout: {
    float: 'right',
    margin: '24px',
  },
  total: {
    fontSize: '1.2em',
    color: 'rgb(53, 97, 85)',
    marginRight: '16px',
    fontWeight: '600',
    verticalAlign: 'bottom',
  },
  continueBtn: {
    marginLeft: '10px',
    textDecoration: 'none',
  },
  itemShop: {
    display: 'block',
    fontSize: '0.90em',
    color: '#78948f',
  },
  removeButton: {
    fontSize: '0.8em',
  },
}));

const CartItems = props => {
  let [cartItems, setCartItems] = useState([]);
  const numberWithCommas = (x, currency) => {
    let number = x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    if (currency === '') {
      return `${number} `
    }
    else {
      return `$${number}`
    }
  };

  useEffect(() => {

    if (!cartItems.length > 0) {
      console.log("Empty Cart Called");
      setCartItems(cart.getCart());
    }
  }, []);

  // const handleChange = index => event => {
  //   setCartItems(cartItems);
  //   cart.updateCart();
  // };

  // const getTotal = () => {
  //   return cartItems.reduce((a, b) => {
  //     return a + b.product.price;
  //   }, 0);
  // };

  const removeItem = index => {
    let cartItems = cart.removeItem(index);
    setCartItems(cartItems);
  };
  const classes = useStyles();
  const { t } = props;
  if (cartItems[0] === 'empty') {
    cartItems = [];
  }
  return (
    <div className={classes.card}>
      <div type="title" className={classes.title}>
        {t('Saved Items')}
      </div>
      {cartItems.length > 0 ? (
        <span>
          {cartItems.map((item, i) => {
            return (
              <span key={i}>
                <div className={classes.cart}>
                  {item.product.images && (
                    <img
                      className={classes.cover}
                      src={item.product.images[0]}
                      alt={item.product.name}
                      onError={(e)=>{e.target.onerror = null; e.target.src=placeholderImage}} 
                    />
                  )}
                  <div className={classes.details}>
                    <div className={classes.content}>
                      <Link to={'/apartment/' + transliterate(item.product.name).replace(/[\. ,:()-]+/g, "-") + '/' + item.product._id}>
                        <div
                          type="title"
                          component="h3"
                          className={classes.productTitle}
                          color="primary"
                        >
                          {item.product.name}
                        </div>
                      </Link>
                      <div>
                        <div
                          type="subheading"
                          component="h3"
                          className={classes.price}
                          color="primary"
                        >
                          {numberWithCommas(item.product.price, item.product.currency)}
                        </div>
                      </div>
                    </div>
                    <div className={classes.subheading}>
                      <BitOlxButton
                        title={t('Remove')}
                        onClick={() => removeItem(i)}
                      />
                    </div>
                  </div>
                </div>
                <Divider />
              </span>
            );
          })}
          <div className={classes.checkout}>
            <Link to="/home" className={classes.continueBtn}>
              <BitOlxButton title={t('Back')} />
            </Link>
          </div>
        </span>
      ) : (
          <div
            style={{ margin: '16px' }}
            type="subheading"
            component="h3"
            color="primary"
          >
            {t('No items have been saved for later.')}
          </div>
        )}
    </div>
  );
};

const NamespaceComponent = withTranslation()(CartItems);
export default NamespaceComponent;
