import React, { Component } from 'react';
import { CardActions, CardContent } from 'material-ui/Card';
import TextField from 'material-ui/TextField';
import Typography from 'material-ui/Typography';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import auth from '../../auth/auth-helper';
import { read, update } from './api-user.js';
import { Redirect } from 'react-router-dom';
import IconButton from 'material-ui/IconButton';
import Edit from 'material-ui-icons/Edit';
import customStyles from './EditProfile.module.scss';
import { withTranslation } from 'react-i18next';
import { BitOlxButton } from '../../commons';
import Dropzone from "react-dropzone";
import request from "superagent";

const styles = theme => ({
  card: {
    maxWidth: 450,
    margin: 'auto',
    textAlign: 'center',
    marginTop: theme.spacing.unit * 5,
    paddingBottom: theme.spacing.unit * 2,
  },
  title: {
    margin: theme.spacing.unit * 2,
    color: theme.palette.protectedTitle,
  },
  error: {
    verticalAlign: 'middle',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 300,
  },
  submit: {
    margin: 'auto',
    marginBottom: theme.spacing.unit * 2,
  },
  subheading: {
    marginTop: theme.spacing.unit * 2,
    color: theme.palette.openTitle,
  },
  input: {
    display: 'none',
  },
  filename: {
    marginLeft: '10px',
  },
});

class EditProfile extends Component {
  constructor({ match }) {
    super();
    this.state = {
      name: '',
      avatar: '',
      telegramId: '',
      username: '',
      password: '',
      background: '',
      spreadsheetUrl: '',
      redirectToProfile: false,
      error: '',
      type: '',
    };
    this.match = match;
  }

  componentDidMount = () => {
    const jwt = auth.isAuthenticated();
    read(
      {
        userId: this.match.params.userId,
      },
      { t: jwt.token }
    ).then(data => {
      if (data.error) {
        this.setState({ error: data.error });
      } else {
        this.setState({
          name: data.name,
          telegramId: data.telegramId,
          seller: data.seller,
          username: data.username,
          avatar: data.avatar,
          background: data.background,
          spreadsheetUrl: data.spreadsheetId
            ? `https://docs.google.com/spreadsheets/d/${data.spreadsheetId}`
            : '',
        });
      }
    });
  };

  onAvatarSelected(file) {
    let percent = {};
    this.percent = {};
    this.notLoadedAvatar = false;
    const cloudName = "bitolx", uploadPreset = "ml_default";
    const url = `https://api.cloudinary.com/v1_1/${
      cloudName
      }/upload`;
    request.post(url)
      .field('upload_preset', uploadPreset)
      .field('file', file)
      .on('progress', (progress) => {
        percent[file.name] = progress.percent;
        this.percent = percent;
        // this.setState({imageObjects:{...this.state.imageObjects,...{[file.name]: {percent:percent[file.name]}}}})
      })
      .end((error, response) => {
        console.log(response);
        try {
          if (response.status === 200) {
            this.setState({ avatar: response.body.url })
          }
          else if (error) {
            console.log("Error");
            this.notLoadedAvatar = true;
            this.percent = {};
          }
        }
        catch (err) {
          // this.setState({imageObjects:{...this.state.imageObjects,...{[file.name]: {percent:-1,file:file}}}})
          this.notLoadedAvatar = true;
          this.percent = {}
        }
      });
  }

  onBackgroundSelected(file) {
    let percent = {};
    this.percent = {};
    this.notLoadedBackground = false;
    const cloudName = "bitolx", uploadPreset = "ml_default";
    const url = `https://api.cloudinary.com/v1_1/${
      cloudName
      }/upload`;
    request.post(url)
      .field('upload_preset', uploadPreset)
      .field('file', file)
      .on('progress', (progress) => {
        percent[file.name] = progress.percent;
        this.percent = percent;
        // this.setState({imageObjects:{...this.state.imageObjects,...{[file.name]: {percent:percent[file.name]}}}})
      })
      .end((error, response) => {
        console.log(response);
        try {
          if (response.status === 200) {
            this.setState({ background: response.body.url })
          }
          else if (error) {
            console.log("Error");
            this.notLoadedBackground = true;
            this.percent = {};
          }
        }
        catch (err) {
          this.notLoadedBackground = true;
          this.percent = {}
        }
      });

  }

  clickSubmit = () => {
    const jwt = auth.isAuthenticated();
    const user = {
      name: this.state.name || undefined,
      telegramId: this.state.telegramId || undefined,
      password: this.state.password || undefined,
      username: this.state.username,
      avatar: this.state.avatar || undefined,
      background: this.state.background || undefined,
      spreadsheetId:
        this.state.spreadsheetUrl
          .replace('https://docs.google.com/spreadsheets/d/', '')
          .substring(0, 44) || undefined,
    };
    update({ userId: this.match.params.userId }, { t: jwt.token }, user).then(
      data => {
        if (data.error) {
          this.setState({ error: data.error });
        } else {
          auth.updateUser(data, () => {
            this.setState({ userId: data._id, redirectToProfile: true });
          });
        }
      }
    );
  };
  handleChange = name => event => {
    this.setState({ [name]: event.target.value });
  };

  checkUploadResult = resultEvent => {
    if (this.state.type === 'avatar-upload') {
      this.setState({ avatar: resultEvent.info.secure_url });
    } else {
      this.setState({ background: resultEvent.info.secure_url });
    }
  };



  render() {
    try {
      window.customerly ? window.customerly("hide") : void (0);
    }
    catch (e) {
      void (0);
    }
    const { classes } = this.props;
    const { t } = this.props;

    if (this.state.redirectToProfile) {
      return <Redirect to={'/user/' + this.state.userId} />;
    }
    return (
      <div className={classes.card}>
        <CardContent>
          <Typography type="headline" component="h2" className={classes.title}>
            {t('Edit Profile')}
          </Typography>
          {this.notLoadedBackground && <Typography variant="caption" color="secondary">{t('An error occurred while uploading the photo. Please try again.')}</Typography>}
          <TextField
            id="background"
            label={t('Background URL')}
            className={classes.textField}
            value={this.state.background}
            onChange={this.handleChange('background')}
            margin="normal"
          />
          <br />
          <TextField
            id="spreadsheetUrl"
            type="text"
            label={t('Google Spreadsheet URL')}
            className={classes.textField}
            value={this.state.spreadsheetUrl}
            onChange={this.handleChange('spreadsheetUrl')}
            margin="normal"
          />
          <br />
          <a
            className="text-underline"
            href="https://docs.google.com/spreadsheets/d/14gUDxXiL2o0JhIRvglAXxoYANSqg9c514MA6ZOH1SAA/edit?usp=sharing"
            target="_blank"
            rel="noopener noreferrer"
          >
            {t('Copy the sample spreadsheet')}
          </a>
        </CardContent>
        <CardActions style={{ justifyContent: 'center' }}>
          <BitOlxButton onClick={this.clickSubmit} title={t('Submit')} />          <Dropzone
            style={{ borderStyle: "hidden" }}
            accept="image/*"
            onDrop={this.onBackgroundSelected.bind(this)}
          >
            <BitOlxButton
              title={t('Upload Background')}
            />
          </Dropzone>
        </CardActions>
      </div>
    );
  }
}

EditProfile.propTypes = {
  classes: PropTypes.object.isRequired,
};

const NamespaceComponent = withTranslation()(EditProfile);
export default withStyles(styles)(NamespaceComponent);
