import React, { Component } from 'react';
import AllProducts from './AllProducts';
import Species from './Species';
import { withTranslation } from 'react-i18next';
import Search from '../Home/Seach/Search.js';
import windowSize from 'react-window-size';
import { fetchFilters, deleteQuery } from '../../utils/filters';

class ProductsContainer extends Component {
    constructor() {
        super();
        this.state = {
            queryObj: {}
        }
        this.deleteQuery = deleteQuery.bind(this);
    }
    render() {
        return (
            <div>
                <Search
                    onChange={fetchFilters.bind(this)}
                /><Species />
                <AllProducts
                    queryObjProps={this.state.queryObj}
                />
            </div>
        );
    }
};

export default windowSize(withTranslation()(ProductsContainer));
