import React, { useState } from 'react';
import styles from './BitOlxFilter.module.scss';
import FullScreenDialog from "./fullScreenDialog";
import FilterDrawer from "./filterDrawer";
import filterData from './mocks/filterItem.json';
import Dropdown from 'react-bootstrap/Dropdown';
import windowSize from 'react-window-size';
import defaultFilter from './mocks/default_filter.json';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Checkbox } from 'primereact/checkbox';
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import Container from 'react-bootstrap/Container';
import { withTranslation, useTranslation } from 'react-i18next';

const CustomRangeMenu = React.forwardRef(
  (
    { children, style, className, 'aria-labelledby': labeledBy, onChange },
    ref
  ) => {
    const [value, setValue] = useState({});

    const handleChange = e => {
      const name = e.target.name;
      const newValue = { ...value, [name]: e.target.value };
      setValue(newValue);
    };

    const { t } = useTranslation();

    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <ul className="list-unstyled">{children}</ul>
        <form onSubmit={(e) => {
          e.preventDefault();
          onChange(value);
        }}>
          <div className={styles.range_style_row}>
            <Form.Control
              type="number"
              min="0"
              name="min"
              value={value.min}
              onChange={handleChange}
              className={styles.spacing_control}
              placeholder={t('Min')}
            />
            <Form.Control
              type="number"
              min="0"
              name="max"
              value={value.max}
              onChange={handleChange}
              className={styles.spacing_control}
              placeholder={t('Max')}
            />
            <input type="submit" style={{ display: "none" }} />
          </div>
        </form>
      </div>
    );
  }
);

const CustomSimpleMenu = React.forwardRef(
  ({ children, style, className, 'aria-labelledby': labeledBy }, ref) => {
    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <ul className="list-unstyled">
          {children}
        </ul>
      </div>
    );
  }
);

const CheckBoxDropDownItem = props => {
  const {
    option,
    index,
    selectedBoxs,
    onSelectedCheckboxs,
  } = props;
  const { t } = useTranslation();

  return (
    <div
      className={`p-col-12 ${styles.dropdown_item}`}
    >
      <Checkbox
        inputId={`cb${index}`}
        value={option}
        onChange={onSelectedCheckboxs}
        checked={selectedBoxs.includes(option)}
      />
      <label htmlFor="cb1" className="p-checkbox-label mb-0">
        {t(option)}
      </label>
    </div>
  );
};

const BitOlxCustomDropDown = props => {
  const { filterItem, onChange } = props;
  const [selectedBoxs, setSelectedBoxs] = useState([]);
  const { t } = useTranslation();

  const onSelectedCheckboxs = e => {
    let selectedItems = [...selectedBoxs];
    if (e.checked) selectedItems.push(e.value);
    else selectedItems.splice(selectedItems.indexOf(e.value), 1);
    setSelectedBoxs(selectedItems);
    onChange(filterItem.title, selectedItems);
  };

  return (
    <div style={{ flex: '1', position: 'relative' }}>
      {filterItem.type === 'only_item' && (
        <Dropdown>
          <Dropdown.Toggle
            variant="success"
            id="dropdown-basic"
            className={styles.drop_down_new_style}
          >
            <span>{t(filterItem.title)}</span>
          </Dropdown.Toggle>

          <Dropdown.Menu
            as={CustomSimpleMenu}
            className={
              props.styled ? styles.item_right_main : styles.item_right
            }
          >
            {filterItem.options.map((option, index) => {
              return (
                <Dropdown.Item
                  key={index}
                  onClick={() => {
                    props.onChange(filterItem.title, option);
                  }}
                  className={styles.dropdown_item}
                  eventKey={`${index}`}
                >
                  {t(option)}
                </Dropdown.Item>
              );
            })}
          </Dropdown.Menu>
        </Dropdown>
      )}
      {filterItem.type === 'checkbox' && (
        filterItem.options.map((option, index) => (
          <div style={{width: '150px'}}>
          <CheckBoxDropDownItem
            key={index}
            option={t(option)}
            index={index}
            onChange={props.onChange.bind(this)}
            filterItem={filterItem}
            selectedBoxs={selectedBoxs}
            onSelectedCheckboxs={onSelectedCheckboxs}
          />
          </div>
        ))
      )}

      {filterItem.type !== 'checkbox_items' && filterItem.type !== 'checkbox' && filterItem.type !== 'only_item' && (
        <Dropdown>
          <Dropdown.Toggle
            variant="success"
            id="dropdown-basic"
            className={styles.drop_down_new_style}
          >
            <span>{t(filterItem.title)}</span>
          </Dropdown.Toggle>
          {filterItem.type === 'range' && (
            <Dropdown.Menu
              as={CustomRangeMenu}
              className={styles.item_right}
              onChange={value => onChange(filterItem.title, value)}
            >
              {filterItem.options.map((option, index) => (
                <Dropdown.Item
                  onClick={e => {
                    props.onChange(filterItem.title, option);
                  }}
                  className={styles.dropdown_item}
                  eventKey={`${index}`}
                  key={`${index}`}
                >
                  {t(option)}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          )}
        </Dropdown>
      )}
    </div>
  );
};

export class BitOlxFilter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isExpand: false,
      filter: {},
    };
  }

  clickSubmit = (type, value) => {
    this.setState({
      filter: { ...this.state.filter, [type]: value },
    });
    this.props.onChange(type, value);
  };

  renderFilterItem = (filter, index) => {
    return (
      <div
        className={styles.filter_item}
        onClick={() => {
          if (filter.expand) this.setState({ isExpand: !this.state.isExpand });
        }}
        key={`${index}`}
      >
        <BitOlxCustomDropDown
          onChange={this.clickSubmit.bind(this)}
          styled
          filterItem={filter}
        />
      </div>
    );
  };

  renderFilterItemMobile = (filter, index) => {
    return (
      <div
        className={styles.filter_item}
        onClick={() => {
          if (filter.expand) this.setState({ isExpand: !this.state.isExpand });
        }}
        key={`${index}`}
      >
        <FilterDrawer
          onChange={(value) => this.clickSubmit(filter.title, value)}
          styled
          filterItem={filter}
        />
      </div>
    );
  };

  render() {
    const { data } = filterData;
    // const { filter } = this.state;
    const { windowWidth } = this.props;
    const categoryFilter = defaultFilter;
    return (
      <div id={styles.filter_main_container}>
        <div className={styles.filter_container}>
          {windowWidth > 760 ?
            data.map((filter, index) => this.renderFilterItem(filter, index))
            :
            data.map((filter, index) => this.renderFilterItemMobile(filter, index))}
        </div>
        {this.state.isExpand && (
          windowWidth < 760 ? (
            <FullScreenDialog filterData={categoryFilter} onChange={this.clickSubmit.bind(this)} />
          ) : (
              <div>
                <Container className={styles.drop_down_filter_container}>
                  <Row>
                    {categoryFilter.data.map((filterItem, index) => (
                      <Col key={index} md={4} className={styles.col_container_style}>
                        <BitOlxCustomDropDown
                          onChange={this.clickSubmit.bind(this)}
                          filterItem={filterItem}
                        />
                      </Col>
                    ))}
                  </Row>
                </Container>
              </div>
            )
        )}
      </div>
    );
  }
}

export default windowSize(withTranslation()(BitOlxFilter));
