let number = '0123456789';
const association = 'jbyarlpqnt';

// input a string, to get string back remove first and last chars. Letter case does not matter i.e. LowerCase or UpperCase - the same. to decrypt use this 'jbyarlpqnt' (10 chars, from 0 to 9 - association)
function decrypt(code) {
    let symbols = String(code).toLowerCase();
    symbols = symbols.substring(1, symbols.length - 1);
    let result = [];
    for (var j = 0; j < code.length; j++)
        for (var i = 0; i < 10; i++) {
            if (symbols[j] == association[i])
                result[j] = number[i]
        }

    return result.join('');
}

export default decrypt;
