import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import StarIcon from '@material-ui/icons/Star';
import StarBorderIcon from '@material-ui/icons/StarBorder'
import IconButton from '@material-ui/core/IconButton';
import 'fetch-everywhere';
import NoPreview from '../../assets/img/no-preview.jpg';
import { read, listRelated } from '../product/api-product.js';
import Grid from "material-ui/Grid/Grid";
import Map from './Map';
import ReactModal from 'react-modal';
import LazyLoad from 'react-lazy-load';
import { Carousel } from 'react-responsive-carousel';
import Helmet from 'react-helmet';
import './SingleProduct.scss';
import Cross from './img/cancel.svg';
import { withTranslation } from 'react-i18next';
import BitOlxButton from '../../commons/BitOlxButton';
import cart from "../cart/cart-helper";
import windowSize from 'react-window-size';
import Divider from "material-ui/Divider/Divider";
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import NoSSR from 'react-no-ssr';
import mapImage from './img/map.jpg';
import placeholderImage from '../../assets/img/placeholder.jpg';
import config from '../../config';
import rCounter from './rooms-counter';

const modalStyles = {
  content: {
    top: '50%',
    left: '5%',
    right: '5%',
    bottom: 'unset',
    padding: 0,
    border: 'none',
    background: 'none',
    borderRadius: '0',
    transform: 'translateY(-50%)',
    margin: '0 auto',
  },
};

class SingleProduct extends Component {
  constructor() {
    super();
    this.state = {
      isPrice: true,
      product: null,
      isFullDesc: false,
      isFullPic: false,
      currentPic: '',
      suggestions: [],
      showModal: false,
      activeImage: 0,
      pictureWidth: '',
      inCart: false,
    };
    this.handleOpenModal = this.handleOpenModal.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
  }

  setType(value) {
    this.setState({ type: value });
  }

  loadProduct(productId) {
    read({ productId: productId }).then(data => {
      if (data.error) {
        this.setState({ error: data.error });
      } else {
        this.setState({ product: data }, () => this.setState({ inCart: cart.getCart().find((product) => (product.product._id === this.state.product._id)) }));
        this.setState({ currentPic: data.images[0] });
        listRelated({
          productId: data._id,
        }).then(data => {
          if (data.error) {
            console.log(data.error);
          } else {
            this.setState({ suggestions: data });
          }
        });
      }
    });
  }

  componentDidMount() {
    let ID = window.location.href.substring(
      window.location.href.lastIndexOf('/') + 1
    );
    this.loadProduct(ID);

  }

  numberWithCommas(x, currency) {
    let number = x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    if (currency == '') {
      return `${number} `
    }
    else {
      return `$${number}`
    }
  }

  expandContent() {
    let product_element = document.getElementById('pics-limit');
    this.setState({ isFullPic: !this.state.isFullPic });
    if (!this.state.isFullPic) {
      product_element.classList.remove('product-listing-limit');
    } else {
      product_element.classList.add('product-listing-limit');
    }
  }

  handleOpenModal(index) {
    this.setState({ showModal: true, activeImage: index });
  }

  handleCloseModal() {
    this.setState({ showModal: false });
  }

  addToCart = (e, item) => {
    cart.addItem(item, () => this.setState({ inCart: cart.getCart().find((product) => (product.product._id === this.state.product._id)) }, () => console.log(this.state.inCart)));
  };

  removeCart = (e, item) => {
    e.preventDefault();
    this.setState({ cart: cart.removeItem(item) }, () => this.setState({ inCart: false }))
  };

  render() {
    const { t, windowWidth } = this.props;
    let product = this.state.product;

    let featureImages = [];
    if (product && product.images) {
      product.images.forEach((element, index) => {
        featureImages.push(
          <LazyLoad key={index} debounce={false} offsetVertical={500}>
            <img
              key={index}
              src={element}
              onLoad={(e) => {
                if (e.target.naturalHeight == 500 && e.target.naturalWidth == 500 && e.target.src.indexOf('apollo.olxcdn.com') != -1)
                  e.target.src = placeholderImage
              }}
              onError={(e) => { e.target.onerror = null; e.target.src = placeholderImage }}
              alt=" | CITVY -  "
              onClick={e => this.handleOpenModal(index)}
            />
          </LazyLoad>
        );
      });
      featureImages.shift();
    }
    try {
      window.customerly ? window.customerly("hide") : void (0);
    }
    catch (e) {
      void (0);
    }

    return (
      <> {product ? <>{
        windowWidth > 850 ? (
          <main role="main" id="container" className="main-container push">
            <div
              itemType="http://schema.org/Product"
              itemScope
              className="product"
            >
              <Helmet>
                <meta charSet="utf-8" />
                <title>{product.name}</title>
                <meta property="og:title" content={product.name} />
                <meta property="og:type" content="product" />
                <meta property="og:url" content={window.location.href} />
                <meta name="description" content={product.description} />
                <meta property="og:site_name" content="CITVY" />
                <meta property="og:image" content={product.images[0]} />
                <meta property="og:image:type" content="image/jpeg" />
                <meta property="og:url" content={window.location.href} />
                <meta property="twitter:title" content={product.name} />
                <meta property="twitter:image" content={product.images[0]} />
                <meta
                  property="twitter:description"
                  content={product.description}
                />
                <meta name="twitter:card" content="summary" />
                <meta itemProp="mpn" content={product._id} />
                <link itemProp="image" content={product.images[0]} />
                <meta itemProp="description" content={product.description} />
                <div
                  itemProp="aggregateRating"
                  itemType="http://schema.org/AggregateRating"
                  itemScope
                >
                  <meta itemProp="reviewCount" content={Number(1 + Math.floor(Math.random() * Math.floor(19)))} />
                  <meta itemProp="ratingValue" content={'4.' + Math.floor(Math.random() * Math.floor(9))} />
                  <meta itemProp="price" content={Math.floor(product.price)} />
                </div>
                <meta itemProp="sku" content={product._id} />
                <div
                  itemProp="brand"
                  itemType="http://schema.org/Thing"
                  itemScope
                >
                  <meta itemProp="name" content="CITVY" />
                </div>
              </Helmet>
              <div className="content">
                <div className="product-title-1" itemProp="name">
                  {this.state.inCart ? (
                    <IconButton
                      onClick={(e) => this.removeCart(e, product)}>
                      <StarIcon />
                    </IconButton>
                  ) : (
                      <IconButton onClick={(e) => this.addToCart(e, product)}>
                        <StarBorderIcon />
                      </IconButton>
                    )}
                  {product.name}
                </div>
                <div className="product-listing">
                  <ReactModal
                    id="photos_modal"
                    shouldCloseOnEsc={true}
                    shouldCloseOnOverlayClick={true}
                    onRequestClose={this.handleCloseModal}
                    style={modalStyles}
                    isOpen={this.state.showModal}
                    contentLabel="Photos Modal"
                  >
                    <img
                      src={Cross}
                      className="cross"
                      alt="Cross"
                      onClick={this.handleCloseModal}
                    />
                    <Carousel
                      className="carousel-product"
                      showThumbs={false}
                      useKeyboardArrows
                      dynamicHeight
                      emulateTouch
                      selectedItem={this.state.activeImage}
                    >
                      {this.state.product &&
                        this.state.product.images.map((imageUrl, index) => {
                          return (
                            <img
                              className="object_fit"
                              key={index}
                              alt=" | CITVY -  "
                              src={imageUrl}
                              onLoad={(e) => {
                                if (e.target.naturalHeight == 500 && e.target.naturalWidth == 500 && e.target.src.indexOf('apollo.olxcdn.com') != -1)
                                  e.target.src = placeholderImage
                              }}
                              onError={(e) => { e.target.onerror = null; e.target.src = placeholderImage }}
                            />
                          );
                        })}
                    </Carousel>
                  </ReactModal>
                  <div
                    id="pics-limit"
                    className="product-image product-listing-limit"
                  >
                    <div className="img-root">
                      <figure
                        itemProp="image"
                        itemScope
                        itemType="http://schema.org/ImageObject"
                        onClick={e => this.handleOpenModal(0)}
                      >
                        {product.images ? (
                          <LazyLoad debounce={false} offsetVertical={500}>
                            <img
                              itemProp="url"
                              className="image-demo"
                              alt={
                                product.name + ' | CITVY -  '
                              }
                              src={product.images[0]}
                              onLoad={(e) => {
                                if (e.target.naturalHeight == 500 && e.target.naturalWidth == 500 && e.target.src.indexOf('apollo.olxcdn.com') != -1)
                                  e.target.src = placeholderImage
                              }}
                              onError={(e) => { e.target.onerror = null; e.target.src = placeholderImage }}
                            />
                          </LazyLoad>
                        ) : (
                            <LazyLoad debounce={false} offsetVertical={500}>
                              <img
                                itemProp="url"
                                className="image-demo"
                                alt={
                                  product.name + ' | CITVY -  '
                                }
                                src={NoPreview}
                              />
                            </LazyLoad>
                          )}
                      </figure>
                    </div>
                    {featureImages}
                  </div>

                  <div className="product-description">
                    <div>
                      <div className="tm-description">
                        {t('Published')}&nbsp;
                        {product.updated.slice(5, 10).replace(/-/g, '.')}{' '}
                        {t('at')} {product.updated.slice(11, 16)}
                      </div>
                      <div
                        itemProp="aggregateRating"
                        itemType="http://schema.org/AggregateRating"
                        itemScope
                      >
                        <meta itemProp="reviewCount" content={Number(1 + Math.floor(Math.random() * Math.floor(19)))} />
                        <meta itemProp="ratingValue" content={'4.' + Math.floor(Math.random() * Math.floor(9))} />
                      </div>
                      {(product.otherObjects && !product.otherObjects.daily) && product.price < 20 ?
                        <p className="price">
                           
                        </p>
                        :
                        <p className="price">
                          <meta
                            itemProp="price"
                            content={Math.ceil(product.price)}
                          />
                          <meta itemProp="priceCurrency" content="USD" />
                          {this.numberWithCommas(Math.floor(product.price), product.currency)}
                          <div className="rent-mo">&nbsp;/ {(product.otherObjects && product.otherObjects.daily) ? '' : ''}</div>
                        </p>
                      }
                      <div className="flexify">
                        {product.owner && (
                          <BitOlxButton
                            title={t('Reserve')}
                            fullWidth
                            onClick={() =>
                              window.location.href = config.socialURL + '/signup?s=' + product.telephone
                            }
                          />
                        )}
                      </div>
                      <div className="flexify">
                        {product.rooms &&
                          <div>
                            {`${product.rooms} ${rCounter(product.rooms)}`}
                          </div>
                        }
                        <div>
                          {product.area && `${product.area} `}
                        </div>
                        <div>
                          {product.re_type}
                        </div>
                        <div>
                          {product.floor && `${product.floor}- `}
                        </div>

                      </div>
                    </div>
                    <NoSSR>
                      <Map
                        location={{
                          latitude: parseFloat(product.location.latitude),
                          longitude: parseFloat(product.location.longitude),
                        }}
                      />
                    </NoSSR>
                    <br />

                    <div className="description" itemProp="description">
                      <p className="hide-content"> {t('details:')}</p>
                      {product.description.length > 92 ?
                        <div
                          className="tm-description"
                          dangerouslySetInnerHTML={{
                            __html: product.description,
                          }}
                        /> : <div
                          className="tm-description"
                          dangerouslySetInnerHTML={{
                            __html: product.description + '<br><br> <b>       ,       .</b>',
                          }}
                        />
                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </main>
        ) : (
            <>
              <Helmet>
                <meta charSet="utf-8" />
                <title>{product.name}</title>
                <meta property="og:title" content={product.name} />
                <meta property="og:type" content="product" />
                <meta property="og:url" content={window.location.href} />
                <meta name="description" content={product.description} />
                <meta property="og:site_name" content="CITVY" />
                <meta property="og:image" content={product.images[0]} />
                <meta property="og:image:type" content="image/jpeg" />
                <meta property="og:url" content={window.location.href} />
                <meta property="twitter:title" content={product.name} />
                <meta property="twitter:image" content={product.images[0]} />
                <meta
                  property="twitter:description"
                  content={product.description}
                />
                <meta name="twitter:card" content="summary" />
                <meta itemProp="mpn" content={product._id} />
                <link itemProp="image" content={product.images[0]} />
                <meta itemProp="description" content={product.description} />
                <div
                  itemProp="aggregateRating"
                  itemType="http://schema.org/AggregateRating"
                  itemScope
                >
                  <meta itemProp="reviewCount" content={Number(1 + Math.floor(Math.random() * Math.floor(19)))} />
                  <meta itemProp="ratingValue" content={'4.' + Math.floor(Math.random() * Math.floor(9))} />
                  <meta itemProp="price" content={Math.floor(product.price)} />
                </div>
                <meta itemProp="sku" content={product._id} />
                <div
                  itemProp="brand"
                  itemType="http://schema.org/Thing"
                  itemScope
                >
                  <meta itemProp="name" content="CITVY" />
                </div>
              </Helmet>
              <Carousel
                showArrows={false}
                showThumbs={false}
                statusFormatter={() => {
                  return <div>
                    {this.state.inCart ? (
                      <IconButton
                        className="cart-button"
                        onClick={(e) => this.removeCart(e, product)}>
                        <StarIcon fontSize="large" />
                      </IconButton>
                    ) : (
                        <IconButton
                          className="cart-button"
                          onClick={(e) => this.addToCart(e, product)}
                        >
                          <StarBorderIcon fontSize="large" />
                        </IconButton>
                      )}
                  </div>
                }}
              >
                {this.state.product && this.state.product.images.map((imageUrl, index) => {
                  return (
                    <div key={index} >
                      <figure className="product-figure-img-single">
                        <img className="carousel-image-single" alt="slide" src={imageUrl} onError={(e) => { e.target.onerror = null; e.target.src = placeholderImage }} onLoad={(e) => {
                          if (e.target.naturalHeight === 500 && e.target.naturalWidth === 500 && e.target.src.indexOf('apollo.olxcdn.com') !== -1)
                            e.target.src = placeholderImage
                        }} />
                      </figure>
                    </div>
                  )
                })}
              </Carousel>
              <div className="info-container">
                <div>
                  <div className="mobile-name">
                    {product.name}
                  </div>
                </div>
                {(product.otherObjects && !product.otherObjects.daily) && product.price < 20 ? <div className="mobile-price">
                   
                </div>
                  :
                  <div className="mobile-price">
                    {this.numberWithCommas(Math.floor(product.price), product.currency)}&nbsp;/ {(product.otherObjects && product.otherObjects.daily) ? '' : ''}
                  </div>
                }
                <div className="features">
                  {product.rooms && <div className="prop">{`${product.rooms} ${rCounter(product.rooms)}`}</div>}
                    {product.area && <div className="prop">{`${product.area} `}</div>}
                  {product.re_type && <div className="prop">{t(product.re_type)}</div>}
                    {product.floor && <div className="prop">{`${product.floor}- `}</div>}
                </div>
                <Divider />
                <div className="map-container">
                  <div className="address-container">
                    <LocationOnIcon fontSize="large" />
                    {product.district}
                  </div>
                  <div>
                    <a href={`https://www.google.com/maps/place/${product.location.latitude}+${product.location.longitude}`}>
                      <img className="map-image" src={mapImage} alt="Location" />
                    </a>
                  </div>
                </div>
                {/*<Divider style={{margin:"11px"}}/>*/}
                <div className="description">
                  <p className="hide-content"> {t('details:')}</p>
                  <div
                    className="tm-description"
                    dangerouslySetInnerHTML={{
                      __html: product.description,
                    }}
                  />
                </div>
                <div className="listed-info">
                  {t('Published')}&nbsp;
                  {product.updated.slice(5, 10).replace(/-/g, '.')}{' '}
                  {t('at')} {product.updated.slice(11, 16)}
                </div>
              </div>

              <div
                className="sticky-container"
              >
                <Grid
                  container
                  justify="space-between"
                  alignItems="center"
                  spacing={1}
                >
                  <Grid
                    item
                    xs={12}
                  >
                    <button onClick={() =>
                      window.location.href = config.socialURL + '/signup?s=' + product.telephone
                    } className="sticky-button colored-button">{t("Reserve")} </button>
                  </Grid>
                  {/* <Grid
                    item
                    xs={6}
                  >
                     <button className="sticky-button transparent-button">{t("Message")}</button>
                  </Grid> */}
                </Grid>
              </div>
            </>
          )}
      </> : null}</>
    );
  }
}

export default withRouter(windowSize(withTranslation()(SingleProduct)));
