import React, { Component } from 'react';
import axios from 'axios';
import { BitOlxFilter } from '../../../commons';
import styles from '../../../containers/ProductComponent/ProductComponent.module.scss';
import config from "../../../config.json";
import { withTranslation } from 'react-i18next';
import './Search.css';

class Search extends Component {
  state = {
    search: '',
    products: []
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
    console.log(event.target.value);
    axios
      .get(
        `${config.apiURL}/bitolx/api/products/search?search=` +
        event.target.value
      )
      .then(response => {
        this.setState({ products: response.data });
      })
      .catch(err => {
        console.log(err);
      });
  };

  handleFieldsChange(type, value) {
    this.props.onChange(type, value);
  }

  componentDidMount() {
    const it = this;
    var x, i, j, selElmnt, a, b, c;
    /*look for any elements with the class "custom-select":*/
    x = document.getElementsByClassName('custom-select');
    for (i = 0; i < x.length; i++) {
      selElmnt = x[i].getElementsByTagName('select')[0];
      /*for each element, create a new DIV that will act as the selected item:*/
      a = document.createElement('DIV');
      a.setAttribute('class', 'select-selected');
      a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML;
      x[i].appendChild(a);
      /*for each element, create a new DIV that will contain the option list:*/
      b = document.createElement('DIV');
      b.setAttribute('class', 'select-items select-hide');
      for (j = 1; j < selElmnt.length; j++) {
        /*for each option in the original select element,
        create a new DIV that will act as an option item:*/
        c = document.createElement('DIV');
        c.innerHTML = selElmnt.options[j].innerHTML;
        c.addEventListener('click', function (e) {
          it.setState({ category: e.path[0].innerText });
          /*when an item is clicked, update the original select box,
          and the selected item:*/
          var y, i, k, s, h;
          s = this.parentNode.parentNode.getElementsByTagName('select')[0];
          h = this.parentNode.previousSibling;
          for (i = 0; i < s.length; i++) {
            if (s.options[i].innerHTML === this.innerHTML) {
              s.selectedIndex = i;
              h.innerHTML = this.innerHTML;
              y = this.parentNode.getElementsByClassName('same-as-selected');
              for (k = 0; k < y.length; k++) {
                y[k].removeAttribute('class');
              }
              this.setAttribute('class', 'same-as-selected');
              break;
            }
          }
          h.click();
        });
        b.appendChild(c);
      }
      x[i].appendChild(b);
      a.addEventListener('click', function (e) {
        /*when the select box is clicked, close any other select boxes,
        and open/close the current select box:*/
        e.stopPropagation();
        closeAllSelect(this);
        this.nextSibling.classList.toggle('select-hide');
        this.classList.toggle('select-arrow-active');
      });
    }
    function closeAllSelect(elmnt) {
      /*a function that will close all select boxes in the document,
      except the current select box:*/
      var x,
        y,
        i,
        arrNo = [];
      x = document.getElementsByClassName('select-items');
      y = document.getElementsByClassName('select-selected');
      for (i = 0; i < y.length; i++) {
        if (elmnt === y[i]) {
          arrNo.push(i);
        } else {
          y[i].classList.remove('select-arrow-active');
        }
      }
      for (i = 0; i < x.length; i++) {
        if (arrNo.indexOf(i)) {
          x[i].classList.add('select-hide');
        }
      }
    }
    document.addEventListener('click', closeAllSelect);
  }

  render() {
    const { t } = this.props;
    let searchProducts = this.state.products.map((product, index) => (
      <a
        className="search-result-product"
        key={index}
        href={'/apartment/' + product.name + '/' + product._id}>
        {product.name}
      </a>
    ));

    return (
      <div
        onBlur={() => {
          setTimeout(() => {
            this.setState({ search: '' });
          }, 150);
        }}
        className="search-component">
        <div className="s003">
          <div className="input-field second-wrap">
            <input
              label="Search products"
              type="search"
              onChange={this.handleChange('search')}
              className="input-field second-wrap"
              margin="normal"
              placeholder={t('Try Podil')}
            />
          </div>
          <div className="input-field third-wrap">
            <svg
              className="svg-inline--fa fa-search fa-w-16"
              aria-hidden="true"
              data-prefix="fas"
              data-icon="search"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 512 512">
              <path
                fill="currentColor"
                d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"
              />
            </svg>
          </div>
        </div>
        {!this.props.map &&
          <div className={`${styles.search_container} scope-bootstrap`}>
            <BitOlxFilter onChange={this.props.onChange} />
          </div>
        }
        {this.state.search && (
          <div className="searched-container">{searchProducts}</div>
        )}
      </div>
    );
  }
}

export default withTranslation('translation')(Search);