import Dropdown from "react-bootstrap/Dropdown";
import styles from "./BitOlxFilter.module.scss";
import React, {useState} from "react";
import Drawer from '@material-ui/core/Drawer';
import List from "@material-ui/core/List";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import { useTranslation } from 'react-i18next';

function FilterDrawer(props) {
    const [openDrawer, setOpenDrawer] = useState(false);
    const [value, setValue] = useState({})
    const {filterItem, onChange} = props;
    const {t} = useTranslation();

    const toggleDrawer = (event, open)  => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift'))
      return;
    setOpenDrawer(open);
    };

    const handleChange = (e) => {
      const name = e.target.name;
      const newValue = { ...value, [name]: e.target.value };
      setValue(newValue);
    };

    return (
        <>
            <Dropdown>
                <Dropdown.Toggle
                  variant="success"
                  id="dropdown-basic"
                  className={styles.drop_down_new_style}
                   onClick={e=>toggleDrawer(e, true)}
                >
                    <span>{t(filterItem.title)}</span>
                </Dropdown.Toggle>
            </Dropdown>
            {filterItem.type && (
                <Drawer anchor='bottom' open={openDrawer} onClose={(e)=> toggleDrawer(e, false)}>

                        <List
                            component="div"
                            disablePadding
                        >
                            <ListItem>
                                <ListItemText primary={t(filterItem.title)} primaryTypographyProps={{variant:"h6"}}/>
                            </ListItem>
                           {filterItem.options && (filterItem.options.map((option,index)=>(
                               <div key={`${index}`}>
                                   <ListItem button onClick={(e)=> {
                                       onChange(option)
                                       toggleDrawer(e, false)
                                   }}>
                                      <ListItemText primary={t(option)} />
                                   </ListItem>
                                   <Divider light/>
                              </div>
                               )
                            )
                           )}
                           <form onSubmit={(e)=>{
                                   e.preventDefault()
                                   onChange(value)
                                   toggleDrawer(e, false)
                               }}>
                               <Grid
                                    container
                                    spacing={2}
                                    direction="row"
                                    justify="center"
                                    alignItems="center"
                               >
                                  <Grid item xs={5}><TextField name="min" placeholder={t("Min")} variant="outlined" onChange={(e)=> handleChange(e,filterItem.title)} /></Grid>
                                  <Grid item xs={5}><TextField name="max" placeholder={t("Max")} variant="outlined" onChange={(e)=> handleChange(e,filterItem.title)}/></Grid>
                                   <input type="submit" style={{display:"none"}}/>
                               </Grid>
                           </form>
                        </List>
                </Drawer>
            )}
        </>
    )
}

export default FilterDrawer ;