import React from 'react';
import styles from './ProductComponent.module.scss';
import { BitOlxFilter, AdsRatingFilter } from '../../commons';
import AllProducts from '../../pages/Products/AllProducts';
// import Search from '../../pages/Home/Seach/Search.js';

function formatNumber(num) {
  return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
}

export default class ProductComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filter: {},
      ordering: { sort: 'created', desc: true },
    };
  }

  triggerProductLoad(id) {
    this.refs.products.loadOneProduct(id);
  }

  triggerAllProducts() {
    this.refs.products.loadProducts();
  }

  handleFieldsChange(type, value) {
    this.props.onChange(type, value);
  }

  setCount(num) {
    this.setState({ count: num })
  }

  setSort(sent) {
    if (sent === 'by listing date')
      this.setState({ ordering: { sort: 'created', desc: true } })
    else if (sent === 'from cheap to expensive')
      this.setState({ ordering: { sort: 'price', desc: false } })
    else if (sent === 'from expensive to cheap')
      this.setState({ ordering: { sort: 'price', desc: true } })
  }

  render() {
    return (
      <div id={styles.product_component}>
        { /*<Search map />*/ }
        <div className={`${styles.search_container} scope-bootstrap`}>
          <BitOlxFilter onChange={this.handleFieldsChange.bind(this)} />
        </div>
        <div className={styles.main_product}>
          <div className="scope-bootstrap">
            <div className={styles.add_rating_main}>
              {this.state.count >= 0 && <AdsRatingFilter rating={formatNumber(this.state.count)} setSort={this.setSort.bind(this)} />}
            </div>
          </div>
          <section>
            <AllProducts
              ref="products"
              oneRow={this.props.oneRow}
              scrollRef={this.props.scrollRef}
              queryObjProps={this.props.queryObjProps}
              ordering={this.state.ordering}
              mapLoaded={this.props.mapLoaded}
              mapNotLoaded={this.props.mapNotLoaded}
              setCount={this.setCount.bind(this)}
              noLatest />
          </section>
        </div>
      </div>
    );
  }
}
