import React, { Component } from 'react';
import 'mapbox-gl/dist/mapbox-gl.css';
import MapGL, { NavigationControl, Layer } from 'react-map-gl';
import Pins from './pins';
import { withTranslation } from 'react-i18next';
const MAPBOX_TOKEN =
  'pk.eyJ1IjoibWluZGZ1bC1tZSIsImEiOiJjazE1YmEza3IwcXBtM2R0Z2MwcDN1a29uIn0.VAYVaKWCntAJ1hjTfCGhsw';

const navStyle = {
  position: 'absolute',
  top: 0,
  right: 0,
  padding: '10px',
};

const paintLayer = {
  'fill-extrusion-color': '#aaa',
  'fill-extrusion-height': {
    type: 'identity',
    property: 'height',
  },
  'fill-extrusion-base': {
    type: 'identity',
    property: 'min_height',
  },
  'fill-extrusion-opacity': 0.6,
};

class Map extends Component {
  constructor(props) {
    super(props);
    this.state = {
      viewport: {},
      searchResultLayer: null,
    };
  }

  mapRef = React.createRef();

  handleViewportChange = viewport => {
    this.setState({
      viewport: { ...this.state.viewport, ...viewport },
    });
  };
  _updateViewport = viewport => {
    this.setState({ viewport });
  };
  componentDidMount() {
    this.setState({
      viewport: {
        latitude: this.props.location.latitude,
        longitude: this.props.location.longitude,
        width: '100%',
        minWidth: '60px',
        height: 400,
        zoom: 15,
      },
    });
  }

  render() {
    const { viewport } = this.state;
    const { t } = this.props;
    return (
      <div>
        <MapGL
          ref={this.mapRef}
          {...viewport}
          mapStyle="mapbox://styles/mapbox/streets-v10"
          onViewportChange={this.handleViewportChange}
          mapboxApiAccessToken={MAPBOX_TOKEN}
        >
          <Layer
            id="3d-buildings"
            type="fill-extrusion"
            source="composite"
            source-layer="building"
            paint={paintLayer}
            filter={['==', 'extrude', 'true']}
            minzoom={14}
          />
          <Pins data={this.props.location} />
          <div className="nav" style={navStyle}>
            <NavigationControl
              showCompass={false}
              onViewportChange={this._updateViewport}
            />
          </div>
        </MapGL>
        <a
          href={`https://www.google.com/maps/place/${this.props.location.latitude}+${this.props.location.longitude}`}
          target="_blank"
          rel="noopener noreferrer"
        >
          {t('Open in Google Maps')}
        </a>
      </div>
    );
  }
}

export default withTranslation()(Map);
