import React, {useState} from "react";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import Collapse from "@material-ui/core/Collapse";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Checkbox from '@material-ui/core/Checkbox';
import Dialog from "@material-ui/core/Dialog";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Slide from "@material-ui/core/Slide";
import {makeStyles} from "@material-ui/core/styles";
import {useTranslation} from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
    backgroundColor:"black",
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
    color:"white",
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function CollapseFilter(props) {
    const [openCollapse, setOpenCollapse] = React.useState(false);
    const [value, setValue] = useState({});
    const {filter, onChange}=props;
    const {t} = useTranslation();

    const handleChange = (e,type) => {
      const name = e.target.name;
      const newValue = { ...value, [name]: e.target.value };
      setValue(newValue);
      // onChange(type,newValue);
    };
    const handleClickCollaplse = () => {
    setOpenCollapse(!openCollapse);
  };

    return (<>
        <ListItem button onClick={handleClickCollaplse}>
                <ListItemText primary={t(filter.title)} />
                {openCollapse ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
          <Collapse in={openCollapse} timeout="auto" unmountOnExit>
            {
              filter.type === "only_item" && (
                  <List component="div" disablePadding>
                  {
                      filter.options.map((option,index)=>(
                       <div key={`${index}`}>
                         <ListItem button onClick={()=>onChange(option)}>
                            <ListItemText primary={t(option)} />
                         </ListItem>
                         <Divider light/>
                      </div>
                       ))
                  }
                  </List>
              )
            }
              {filter.type==="range" && (
                <List component="div" disablePadding>
                    <Grid
                        container
                        spacing={2}
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                      <Grid item xs={5}><TextField name="min" placeholder={t("Min")} variant="outlined" onBlur={(e)=> handleChange(e,filter.title)}/></Grid>
                      <Grid item xs={5}><TextField name="max" placeholder={t("Max")} variant="outlined" onBlur={(e)=> handleChange(e,filter.title)}/></Grid>
                    </Grid>
                   {  filter.options.map((option,index)=>(
                       <div key={`${index}`}>
                           <ListItem button onClick={()=>onChange(option)}>
                              <ListItemText primary={t(option)} />
                           </ListItem>
                           <Divider light/>
                      </div>
                       )
                    )
                   }
                </List>
              )}
              {filter.type==="checkbox" && (
                  <>
                    <List component="div" disablePadding>
                      {filter.options.map((option,index)=>(
                        <ListItem key={index} role={undefined} dense button>
                          <ListItemIcon>
                            <Checkbox
                            color="default"
                            onChange={()=> onChange(option)}
                            />
                          </ListItemIcon>
                          <ListItemText  primary={t(option)} />
                        </ListItem>
                      ))}
                    </List>
                  </>
                  )}
          </Collapse>
                <Divider/>
            </>
    );

}

export default function FullScreenDialog(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const filterData = props.filterData.data;
  const {onChange} = props;
  const {t}=useTranslation();

  const handleClose = () => {
    setOpen(false);
  };

  return (
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
            <AppBar className={classes.appBar}>
              <Toolbar>
                <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                  <CloseIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                  {t("More Filters")}
                </Typography>
                <Button autoFocus color="inherit" onClick={handleClose}>
                  {t("Save")}
                </Button>
              </Toolbar>
            </AppBar>
            <List>
                {filterData.map((filter,index)=><CollapseFilter key={`${index}`} filter={filter} onChange={(value)=>onChange(filter.title,value)}/>)}
            </List>
      </Dialog>
  );
}


