import React,{useEffect,useState} from 'react';
import { Link } from 'react-router-dom';
import { Card, CardActionArea } from '@material-ui/core';
import { withTranslation, useTranslation } from 'react-i18next';
import auth from '../../auth/auth-helper';
import config from '../../config.json';
import './Species.scss';

function formatNumber(num) {
  return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
}

const Species = () => {
  const { t, i18n } = useTranslation();
  let url = '/map';
//   if (i18n.language === 'ru') {
//     url = '/--';
//   } else if (i18n.language === 'uk') {
//     url = '/--';
//   }
  const [count,setCount]=useState(0)
  useEffect(()=>{
    fetch(`${config.apiURL}/bitolx/api/products?page=0`,{
      method:'POST',
      headers : {
        'Content-Type': 'application/json'
      }
    }).then((res=> {
      return res.json()
    })).then((resp)=>{
      setCount(resp.count)
    }).catch((err)=>{
      console.error(err)
    })
  },[])

  return (
    <div className="center-wrapper">
      <div className="section-links-wrapper">
        <Link
          to={`${url}`}
          className="section-link section-link--business"
          data-event-category="top_sections"
          data-event-action="business"
          data-event-label="click"
        >
          {
            count>0 && <Card>
              <CardActionArea>
                <div className="section-link__title">
                  <span className="text-black">{t(`Explore properties on Map`, { count: formatNumber(count) })}</span>
                </div>
              </CardActionArea>
            </Card>
          }
        </Link>
        <div className="section-links-inner-wrapper">
          <a
            href={auth.isAuthenticated() ? `https://citvy.com/products/my/id` : `https://s.citvy.com/signup`}
            className="section-link section-link--flat_rent"
            data-event-category="top_sections"
            data-event-action="flat_rent"
            data-event-label="click"
          >
            <Card>
              <CardActionArea>
                <div className="section-link__title">
                  <span>{t('Accept guests')}</span>
                </div>
              </CardActionArea>
            </Card>
          </a>

          <a
            href='https://t.me/citvy_talk'
            target='_blank'
            className="section-link section-link--franchise"
            data-event-category="top_sections"
            data-event-action="franchise"
            data-event-label="click"
          >
            <Card>
              <CardActionArea>
                <div className="section-link__title">
                  <span>{t('Telegram chat')}</span>
                </div>
              </CardActionArea>
            </Card>
          </a>

          <a
            href='/blog'
            target='_blank'
            className="section-link section-link--construction"
            data-event-category="top_sections"
            data-event-action="construction"
            data-event-label="click"
          >
            <Card>
              <CardActionArea>
                <div className="section-link__title">
                  <span>{t('Real Estate Blog')}</span>
                </div>
              </CardActionArea>
            </Card>
          </a>

          <a
            href='/about'
            target='_blank'
            className="section-link section-link--apartments"
            data-event-category="top_sections"
            data-event-action="apartments"
            data-event-label="click"
          >
            <Card>
              <CardActionArea>
                <div className="section-link__title">
                  <span>{t('About us')}</span>
                </div>
              </CardActionArea>
            </Card>
          </a>
        </div>
      </div>
    </div>
  );
};

export default withTranslation()(Species);