import React from 'react';
import { withTranslation } from 'react-i18next';
import StarIcon from './icons/star.svg';
import { Carousel } from 'react-responsive-carousel';
import cart from '../cart/cart-helper.js';
import './AllProducts.scss';
import placeholderImage from '../../assets/img/placeholder.jpg';
import transliterate from 'transliterate';
import rCounter from './rooms-counter';

class Product extends React.Component {
  productImages = (id, imageList, name) => {
    return imageList.map((img, key) => (
      <div className="img-root" key={key}>
        <a target="_blank" href={`/apartment/${name}/${id}`}>
          <figure className="product-figure-img">
            <img alt="Product" className="product-img" src={img} onError={(e) => { e.target.onerror = null; e.target.src = placeholderImage }} onLoad={(e) => {
              if (e.target.naturalHeight == 500 && e.target.naturalWidth == 500 && e.target.src.indexOf('apollo.olxcdn.com') != -1)
                e.target.src = placeholderImage
            }} />
          </figure>
        </a>
      </div>
    ));
  };

  addToCart = (e, item) => {
    e.preventDefault();
    cart.addItem(item);
  };

  numberWithCommas(x) {
    let number = x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    return `$${number}`
  }

  render() {
    const { template, t } = this.props;
    let transliteratedName = transliterate(template.name).replace(/[\%\#\@\$\^\&\?\!\)\(\*\~\`\/\\)]/g, '').replace(/[\. ,:()-]+/g, "-");
    let apartUrl = `/apartment/${transliteratedName}/${template._id}`;
    return (
      template.images[0] ? (
        <div
          style={{ width: this.props.oneRow && '100%' }}
          className="product-item"
        >
          { template.isVerified &&
            <div style={{ position: 'absolute', zIndex: 99, border: 'solid', borderWidth: '1px', padding: '0 6px', borderRadius: '10px', fontSize: '15px', fontWeight: '700', margin: '8px', background: 'white', color: 'black', borderColor: 'black' }}></div>
          }
          <div className="product-image">
            <img
              alt="Product"
              onClick={e => {
                this.addToCart(e, template);
              }}
              className="star-save"
              src={StarIcon}
            />
            <Carousel
              className="product-image-lists"
              showArrows
              showIndicators={false}
              infiniteLoop
              swipeScrollTolerance={1000}
              showStatus={false}
              swipeable={false}
              showThumbs={false}
            >
              {this.productImages(template._id, template.images, transliteratedName)}
            </Carousel>
          </div>
          <a href={apartUrl} target="_blank" className="overlay">
            <div className="overlay-content">
              <div className="product-title text-initial">
                <a target="_blank" href={apartUrl}>{template.name}</a>
              </div>
              <div className="flexi product-content-box">
                <div className="text-initial">
                  {(template.otherObjects && !template.otherObjects.daily) && template.price < 20 ?
                    <div className="product-price">
                      <div className="flexi">  </div>
                      <div className="rent-mo-list">&nbsp;</div>
                    </div>
                    :
                    <div className="product-price">
                      <div className="flexi">{this.numberWithCommas(template.price)} </div>
                      <div className="rent-mo-list">&nbsp;/ {(template.otherObjects && template.otherObjects.daily) ? '' : ''}</div>
                    </div>
                  }

                  <div className="product-sales">
                    {t('Last update')}{' '}
                    {template.updated.slice(0, 10).replace(/-/g, '.')}
                  </div>
                </div>
                <div className="product-buttons">
                  {template.rooms &&
                    <div className="product-rooms">
                      {`${template.rooms} ${rCounter(template.rooms)}`}
                    </div>
                  }
                  <div className="product-sqm">
                    {template.area && `${template.area} `}
                  </div>
                </div>
              </div>
            </div>
          </a>
        </div >
      )
        : null
    );
  }
}

export default withTranslation()(Product);
