import React, { useEffect, useState } from 'react';
import { Button, IconButton } from '@material-ui/core';
import { PhotoCamera } from '@material-ui/icons';
import styles from './BitOlxButton.module.scss';
import BitOlxText from '../BitOlxText/BitOlxText';

export const BitOlxButton = props => {
  const {
    title,
    size,
    onClick,
    fullWidth,
    variant,
    startIcon,
    endIcon,
    className,
  } = props;
  const full = fullWidth ? styles.full_width : '';
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => setWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <div id={styles.btn_container} className={`${full} ${className}`}>
      {width <= 425 && (startIcon || endIcon) ? (
        <IconButton
          className={styles.mobileIcon}
          color="primary"
          aria-label="icon-button"
          component="span"
        >
          <PhotoCamera />
        </IconButton>
      ) : (
        <Button
          variant={variant ? 'variant' : 'contained'}
          onClick={onClick}
          size={size}
          className={`${full}`}
          startIcon={startIcon}
          endIcon={endIcon}
        >
          <BitOlxText text={title} />
        </Button>
      )}
    </div>
  );
};

export default BitOlxButton;
