import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import LazyLoad from 'react-lazy-load';
import auth from '../../auth/auth-helper';
import { read } from './api-user.js';
import { withStyles } from 'material-ui/styles';
import { CardContent } from 'material-ui/Card';
import CameraAlt from 'material-ui-icons/CameraAlt';
import customStyles from './EditProfile.module.scss';
import AllProducts from '../Products/AllProducts';
import { BitOlxButton } from '../../commons';
import './Profile.scss';
import { withTranslation } from 'react-i18next';

const styles = theme => ({
  title: {
    margin: `${theme.spacing.unit * 3}px 0 ${theme.spacing.unit * 2}px`,
    color: theme.palette.protectedTitle,
  },
  // title: {
  //   color: theme.palette.protectedTitle,
  //   margin: '30px 16px 16px',
  // },
  error: {
    verticalAlign: 'middle',
  },
});

class Profile extends Component {
  constructor({ match }) {
    super();
    this.state = {
      user: '',
      redirectToSignin: false,
    };
    this.match = match;
  }
  init = userId => {
    read({ userId: userId }).then(data => {
      if (data.error) this.setState({ redirectToSignin: true });
      else this.setState({ user: data });
    });
  };

  componentWillReceiveProps = props => {
    this.init(props.match.params.userId);
  };
  componentDidMount = () => {
    this.init(this.match.params.userId);
  };

  render() {
    const { t } = this.props;
    return (
      <div id="user-profile">
        <CardContent className="profile-card">
          <div>
            <LazyLoad debounce={false} offsetVertical={500}>
              <img
                alt="Background"
                src={this.state.user.background}
                className="background-img"
              />
            </LazyLoad>
          </div>
          <div className="avatar">
            <LazyLoad debounce={false} offsetVertical={500}>
              <img
                alt="Avatar"
                className={customStyles.avatar_img}
                src={this.state.user.avatar}
              />
            </LazyLoad>
            <p className="user-name">
              {this.state.user.name}
            </p>
            <div>
              {t('Joined')}: {new Date(this.state.user.created).toDateString()}
            </div>
          </div>
          <div className="btn-action">
            {auth.isAuthenticated() &&
              auth.isAuthenticated().user._id === this.match.params.userId && (
                <Link to={'/user/edit/' + this.state.user._id}>
                  <BitOlxButton startIcon={<CameraAlt />} title="" />
                </Link>
              )}
          </div>
        </CardContent>
        <AllProducts userId={this.match.params.userId} />
      </div>
    );
  }
}

const NamespaceComponent = withTranslation()(Profile);
export default withStyles(styles)(NamespaceComponent);
