const list = (page, category, search) => {
  if (!category) {
    category = 'All';
  }
  if (!search) {
    search = '';
  }
  return fetch(
    `${config.apiURL}/bitolx/api/products?category=${category}&page=${page}&search=${search}`,
    {
      method: 'GET',
    }
  )
    .then(response => {
      return response.json();
    })
    .catch(err => console.log(err));
};

export {
  create,
  read,
  update,
  remove,
  listByOwner,
  listRelated,
  list,
  listView,
  listByLord
};
