import React, { Component } from 'react';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import auth from '../../auth/auth-helper';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { create } from './api-product.js';
import { Link, Redirect } from 'react-router-dom';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import DeleteOutlineRoundedIcon from '@material-ui/icons/DeleteOutlineRounded';
import Geo from './Geo';
import BitOlxButton from '../../commons/BitOlxButton';
import 'bootstrap/dist/css/bootstrap.min.css';
import { withTranslation } from 'react-i18next';
import request from 'superagent';
import { Image, Transformation, CloudinaryContext } from 'cloudinary-react';
import ReactDropzone from "react-dropzone";

const styles = theme => ({
  card: {
    maxWidth: 900,
    margin: 'auto',
    textAlign: 'center',
    marginTop: theme.spacing(0) * 5,
    paddingBottom: theme.spacing(2) * 2,
  },
  error: {
    verticalAlign: 'middle',
  },
  title: {
    marginTop: theme.spacing(2) * 2,
    color: theme.palette.openTitle,
    fontSize: '1.6em',
    textAlign: 'center',
  },
  textField: {
    marginLeft: theme.spacing(8) / 8,
    marginRight: theme.spacing(8) / 8,
    width: '100%',
  },
  submit: {
    margin: 'auto',
    marginBottom: theme.spacing(2) * 2,
    textDecoration: 'none',
    width: '150px!important',
  },
  input: {
    display: 'none',
  },
  filename: {
    marginLeft: '10px',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
  },
  btnActions: {
    justifyContent: 'center',
  },
});

class NewProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      description: '',
      images: [],
      imageObjects: {},
      publicIds: [],
      category: 'For Sale by Owner',
      price: '',
      telephone: '',
      floor: '',
      area: '',
      kitchenArea: '',
      livingArea: '',
      publisher: 'owner',
      location: {},
      redirect: false,
      error: '',
    };
    this.handleFieldChange = this.handleFieldChange.bind(this);
    this.handleFieldChange = this.handleFieldChange.bind(this);
    this.onPhotoSelected = this.onPhotoSelected.bind(this);
    this.deleteUploaded = this.deleteUploaded.bind(this);
  }

  handleFieldChange(latitude, longitude) {
    this.setState({
      location: {
        latitude: latitude,
        longitude: longitude,
      },
    });
  }

  handleChange = name => event => {
    this.setState({ [name]: event.target.value });
  };

  componentDidMount() {
    let letterLimitTitle = 70;
    let letterLimit = 9000;

    let textareaTitle = document.getElementById('name');
    let textarea = document.getElementById('multiline-flexible');

    document.getElementById('lettersCountTitle').innerHTML = letterLimitTitle;
    document.getElementById('lettersCount').innerHTML = letterLimit;

    textareaTitle.addEventListener(
      'input',
      function () {
        document.getElementById('lettersCountTitle').innerHTML =
          letterLimitTitle - textareaTitle.value.length;
      },
      false
    );
    textarea.addEventListener(
      'input',
      function () {
        document.getElementById('lettersCount').innerHTML =
          letterLimit - textarea.value.length;
      },
      false
    );
  }

  clickSubmit = () => {
    function stripScripts(s) {
      var div = document.createElement('div');
      div.innerHTML = s;
      var scripts = div.getElementsByTagName('script');
      var i = scripts.length;
      while (i--) {
        scripts[i].parentNode.removeChild(scripts[i]);
      }
      return div.innerHTML;
    }
    let images = this.state.images;
    let finalImages = [];
    var imageExists = image_url => {
      let http = new XMLHttpRequest();

      http.open('HEAD', image_url, false);
      http.send();
      if (http.status === 200) {
        finalImages.push(image_url);
      }
    };

    let imgsLength = images.length;

    for (var i = 0; i < imgsLength; i++) {
      imageExists(images[i]);
    }

    if (!this.state.location) {
      this.setState({ error: 'Address is required' });
    } else {
      const product = {
        name: this.state.name || undefined,
        description: stripScripts(this.state.description) || undefined,
        images: finalImages || undefined,
        category: this.state.category || undefined,
        price: this.state.price || undefined,
        telephone: this.state.telephone || undefined,
        floor: this.state.floor || undefined,
        area: this.state.area || undefined,
        livingArea: this.state.livingArea || undefined,
        kitchenArea: this.state.kitchenArea || undefined,
        publisher: this.state.publisher || undefined,
        location: {
          longitude: this.state.location.longitude,
          latitude: this.state.location.latitude,
          address: this.state.address,
        },
      };

      if (finalImages.length < 3) {
        this.setState({ error: '--!   3 ' });
      } else if (this.state.description.length < 40) {
        this.setState({
          error: ',     40 ',
        });
      } else if (this.state.description.length > 9000) {
        this.setState({
          error: '! 9000+      ',
        });
      } else if (this.state.name.length > 70) {
        this.setState({
          error: '! 70+     ',
        });
      } else if (this.state.price.length > 50) {
        this.setState({
          error: '! 50+     ',
        });
      } else if (this.state.telephone.length > 20) {
        this.setState({
          error: '!     20- ',
        });
      } else {
        const jwt = auth.isAuthenticated();
        for (i = 0; i < 100; i++) {
          window.clearInterval(i);
        }
        create({ ownerId: jwt.user._id }, { t: jwt.token }, product).then(
          data => {
            if (data.error) this.setState({ error: data.error });
            else this.setState({ error: '', redirect: true });
          }
        );
      }
    }
  };

  deleteUploaded(imageObj) {
    Object.keys(this.state.imageObjects).map((image) => {
      if (this.state.imageObjects[image].CloudUrl === imageObj.CloudUrl) {
        delete this.state.imageObjects[image];
      }
    });
    this.setState({ images: this.state.images.filter(image => image !== imageObj.CloudUrl) })
  }

  onPhotoSelected(files) {
    let percent = {};
    this.percent = {};
    this.notLoaded = false;
    const cloudName = "bitolx", uploadPreset = "ml_default";
    const url = `https://api.cloudinary.com/v1_1/${
      cloudName
      }/upload`;
    for (let file of files) {
      request.post(url)
        .field('upload_preset', uploadPreset)
        .field('file', file)
        .field('multiple', true)
        .on('progress', (progress) => {
          percent[file.name] = progress.percent;
          this.percent = percent;
          // this.setState({imageObjects:{...this.state.imageObjects,...{[file.name]: {percent:percent[file.name]}}}})
        })
        .end((error, response) => {
          console.log(response);
          try {
            if (response.status === 200) {
              this.setState({ images: [...this.state.images, response.body.url], imageObjects: { ...this.state.imageObjects, ...{ [file.name]: { CloudUrl: response.body.url, publicId: response.body.public_id } } } }, () => console.log(this.state.imageObjects))
            }
            else if (error) {
              // this.setState({imageObjects:{...this.state.imageObjects,...{[file.name]: {percent:-1,file:file}}}})
              console.log("Error");
              this.notLoaded = true;
              this.percent = {};
            }
          }
          catch (err) {
            // this.setState({imageObjects:{...this.state.imageObjects,...{[file.name]: {percent:-1,file:file}}}})
            this.notLoaded = true;
            this.percent = {}
          }
        });
    }
  }

  render() {
    try {
      window.customerly ? window.customerly("hide") : void (0);
    }
    catch (e) {
      void (0);
    }
    let buttonStyles = {
      position: "absolute",
      top: "50%",
      left: "50%",
      transform: "translate(-50%, -50%)",
      MStransform: "translate(-50%, -50%)",
      backgroundColor: "#555",
      color: "white",
      fontSize: "16px",
      padding: "12px 24px",
      border: "none",
      cursor: "pointer",
      borderRadius: "5px",
      textAlign: "center",
    }
    if (this.state.redirect) {
      return <Redirect to={'/products/my/id'} />;
    }
    const { classes } = this.props;
    const { t } = this.props;

    return (
      <div>
        <Card className={classes.card}>
          <CardContent>
            <div type="headline" component="h2" className={classes.title}>
              <b>{t('New Product')}</b>
            </div>
            <br />
            <CloudinaryContext cloudName="bitolx" uploadPreset="ml_default">
              <div className="position">
                <div className="position_1">{t('Photo')} *</div>
                <div
                  className="position_2"
                >
                  <Grid
                    container
                    spacing={1}
                    direction="row"
                    justify="flex-start"
                    alignItems="center"
                  >
                    <Grid item xs={12}>
                      <ReactDropzone
                        multiple={true}
                        accept="image/*"
                        style={{ position: "relative", width: "100%", borderWidth: "2px", borderColor: "rgb(102, 102, 102)" }}
                        onDrop={this.onPhotoSelected.bind(this)}
                      >
                        {() => {
                          return (
                            <>
                              <div className="icon_area cloudinary-button">
                                <div>
                                  {(this.percent && Object.values(this.percent).some((val) => typeof val === "number"))
                                    ?
                                    (
                                      <svg
                                        className="camera_icon"
                                        width="24"
                                        height="24"
                                        viewBox="0 0 24 24"
                                      >
                                        <path
                                          fill="black"
                                          d="M19 17.712V9.206c0-.137-.111-.205-.247-.205H16c-.553 0-1-.49-1-1.042v-1.31a.65.65 0 0 0-.649-.648H9.649A.65.65 0 0 0 9 6.65v1.309C9 8.512 8.553 9 8 9H5.247C5.111 9 5 9.069 5 9.206v8.506c0 .137.111.289.247.289h13.506c.136 0 .247-.152.247-.29zM18.753 7C19.992 7 21 7.967 21 9.206v8.506C21 18.95 19.992 20 18.753 20H5.247C4.008 20 3 18.95 3 17.71V9.207C3 7.966 4.008 7 5.247 7H7V6.65A2.652 2.652 0 0 1 9.649 4h4.702A2.652 2.652 0 0 1 17 6.65V7h1.753zM12 15c1.103 0 2-.897 2-2s-.897-2-2-2-2 .897-2 2 .897 2 2 2zm0-6c2.206 0 4 1.794 4 4s-1.794 4-4 4-4-1.794-4-4 1.794-4 4-4z"
                                        >
                                          <animateTransform attributeName="transform" type="rotate" from="360 8.4 9.5" to="0 8.4 9.5" dur="0.5s" additive="sum" repeatCount="indefinite" />
                                        </path>
                                      </svg>
                                    ) : (
                                      <svg
                                        className="camera_icon"
                                        width="24"
                                        height="24"
                                        viewBox="0 0 24 24"
                                      >
                                        <path
                                          fill="black"
                                          d="M19 17.712V9.206c0-.137-.111-.205-.247-.205H16c-.553 0-1-.49-1-1.042v-1.31a.65.65 0 0 0-.649-.648H9.649A.65.65 0 0 0 9 6.65v1.309C9 8.512 8.553 9 8 9H5.247C5.111 9 5 9.069 5 9.206v8.506c0 .137.111.289.247.289h13.506c.136 0 .247-.152.247-.29zM18.753 7C19.992 7 21 7.967 21 9.206v8.506C21 18.95 19.992 20 18.753 20H5.247C4.008 20 3 18.95 3 17.71V9.207C3 7.966 4.008 7 5.247 7H7V6.65A2.652 2.652 0 0 1 9.649 4h4.702A2.652 2.652 0 0 1 17 6.65V7h1.753zM12 15c1.103 0 2-.897 2-2s-.897-2-2-2-2 .897-2 2 .897 2 2 2zm0-6c2.206 0 4 1.794 4 4s-1.794 4-4 4-4-1.794-4-4 1.794-4 4-4z"
                                        >
                                        </path>
                                      </svg>
                                    )
                                  }
                                </div>
                                <div>
                                  <p className="camera_let_1">{t('')}</p>
                                </div>
                                <div>
                                  <p className="camera_let_2">{this.state.images.length} {t('')} 20</p>
                                </div>
                              </div>
                              <br />
                            </>
                          )
                        }
                        }
                      </ReactDropzone>
                    </Grid>
                    {
                      Object.values(this.state.imageObjects).map((image) => {
                        return (
                          <Grid item xs={4} md={2}>
                            <div className="img-wrapper">
                              <Image style={{ borderStyle: "solid", borderWidth: "2px", borderColor: "rgb(225,225,239,1)", borderRadius: "15px" }} alt="Uploaded" publicId={image.publicId}>
                                <Transformation height="129" width="129" crop="thumb" />
                              </Image>
                              <div className="img-overlay">
                                <IconButton aria-label="delete" onClick={() => this.deleteUploaded(image)}>
                                  <DeleteOutlineRoundedIcon style={{ backgroundColor: "grey", borderRadius: "6px", color: "white" }} color="primary" />
                                </IconButton>
                              </div>
                            </div>
                          </Grid>
                        )
                      })
                    }
                  </Grid>
                  {this.notLoaded && (<Typography variant="caption" color="secondary">{t('An error occurred while uploading the photo. Please try again.')}</Typography>)}
                </div>
              </div>
            </CloudinaryContext>
            <br />

            <div className="position">
              <div className="position_1">{t('Title')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="name"
                  placeholder={t('What are you selling')}
                  maxLength="70"
                  className={classes.textField}
                  value={this.state.name}
                  onChange={this.handleChange('name')}
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="help-text-title">
              <div id="lettersCountTitle"></div>&nbsp;{t('characters left')}
            </div>
            <div className="position">
              <div className="position_1">{t('Description')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="multiline-flexible"
                  placeholder={t('What is feature')}
                  multiline
                  required
                  rows="8"
                  maxLength="9000"
                  value={this.state.description}
                  onChange={this.handleChange('description')}
                  className={classes.textField}
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <div className="help-text-desc">
              <div id="lettersCount"></div>&nbsp;
              {t('characters left, HTML5 supported')}
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Price')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="price"
                  placeholder={t('Price in USD')}
                  className={classes.textField}
                  value={this.state.price}
                  onChange={this.handleChange('price')}
                  type="text"
                  margin="normal"
                  variant="outlined"
                />
                <br />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Telophone')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="Telephone"
                  placeholder="380XXXXXXXXXX"
                  className={classes.textField}
                  value={this.state.telephone}
                  onChange={this.handleChange('telephone')}
                  type="number"
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Floor')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="select_Floor"
                  placeholder="2"
                  className={classes.textField}
                  value={this.state.floor}
                  onChange={this.handleChange('floor')}
                  type="text"
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Area')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="select_Area"
                  placeholder="60"
                  className={classes.textField}
                  value={this.state.area}
                  onChange={this.handleChange('area')}
                  type="text"
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">
                {t('Kitchen/ Living Area')} *
              </div>
              <div className="position_2 margin_iss">
                <div className="flex">
                  <TextField
                    id="select_LivingArea"
                    placeholder="30"
                    className={classes.textField}
                    value={this.state.livingArea}
                    onChange={this.handleChange('livingArea')}
                    type="text"
                    margin="normal"
                    variant="outlined"
                  />
                  <TextField
                    id="select_KitchenArea"
                    placeholder="15"
                    className={classes.textField}
                    value={this.state.kitchenArea}
                    onChange={this.handleChange('kitchenArea')}
                    type="text"
                    margin="normal"
                    variant="outlined"
                  />
                </div>
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Publisher')} *</div>
              <div className="position_2">
                <Select
                  name="Publisher"
                  id="select_Interm"
                  onChange={this.handleChange('publisher')}
                  value={this.state.publisher}
                  variant="outlined"
                  defaultValue="owner"
                  className="position_3"
                >
                  <MenuItem value="owner">{t('owner')}</MenuItem>
                  <MenuItem value="broker">{t('broker')}</MenuItem>
                </Select>
              </div>
            </div>
            <br />
            <br />
            <div>{t('Enter the address into the search box')}</div>
            <Geo onChange={this.handleFieldChange} />
            {this.state.error && (
              <Typography component="p" color="error">
                <br></br>
                {this.state.error}
              </Typography>
            )}
          </CardContent>
          <CardActions className={classes.btnActions}>
            <BitOlxButton
              fullWidth
              title={t('Submit')}
              onClick={this.clickSubmit}
              className={{ marginRight: '4px' }}
            />
            <Link to={'/'} style={{ width: '100%' }}>
              <BitOlxButton fullWidth title={t('Cancel')} />
            </Link>
          </CardActions>
        </Card>
      </div>
    );
  }
}

NewProduct.propTypes = {
  classes: PropTypes.object.isRequired,
};

const NamespaceComponent = withTranslation()(NewProduct);
export default withStyles(styles)(NamespaceComponent);
