import React, { useState, Component } from 'react';
import { signin } from '../../auth/api-auth.js';
import auth from '../../auth/auth-helper';
import './SignUp.scss';
import config from '../../config';

class SignUp extends Component {
  testFacebookLogin = response => {
    signin(response).then(data => {
      if (data && data.error) {
        console.log(data.error);
      } else {
        auth.authenticate(data, () => {
          console.log(data);
          window.location.href = '/products/new';
        });
      }
    });
  };

  componentDidMount() {
    let url_string = window.location.href; //window.location.href
    let url = new URL(url_string);
    let skey = url.searchParams.get('skey');
    let lkey = url.searchParams.get('lkey');
    let landlord_id = url.searchParams.get('landlord_id');
    let logout = url.searchParams.get('logout');
    let actual_landlord_id = url.searchParams.get('actual_landlord_id');
    let userObj = {
      token: skey || lkey,
      user: {
        _id: url.searchParams.get('_id'),
        name: url.searchParams.get('name'),
        telegramId: url.searchParams.get('telegramId'),
        seller: true,
        username: url.searchParams.get('username'),
      }
    }
    if (actual_landlord_id) {
      auth.authenticate(userObj, (data) => {
        console.log(data);
        window.location.href = `/products/my/${userObj.user._id}?signedup=true`
      });
    }
    else if (skey && !landlord_id) {
      auth.authenticate(userObj, (data) => {
        console.log(data);
        window.location.href = '/map?signedup=true'
      });
    }
    else if (lkey && !landlord_id) {
      auth.authenticate(userObj, (data) => {
        console.log(data);
        window.location.href = '/map?loggedin=true'
      });
    }
    else if (skey && landlord_id) {
      auth.authenticate(userObj, (data) => {
        console.log(data);
        window.location.href = config.socialURL + '/messages'
      });
    }
    else if (lkey && landlord_id) {
      auth.authenticate(userObj, (data) => {
        console.log(data);
        window.location.href = config.socialURL + '/messages'
      });
    }
    else if (logout) {
      auth.signout((data) => {
        console.log(data);
        window.location.href = config.socialURL + '/login'
      });
    }
  }
  render() {
    return (
      <div className="login-bg">
        <main role="main" id="container" className="main-container push">
          <div className="product">
          </div>
        </main>
      </div>
    );
  }
};

export default SignUp;