import React,{useState,useEffect} from 'react';
import styles from './AdsRatingFilter.module.scss';
import filterData from '../BitOlxFilter/mocks/filterAds.json';
import BitOlxText from '../BitOlxText/BitOlxText';
import Dropdown from 'react-bootstrap/Dropdown';
import stylesFilter from '../BitOlxFilter/BitOlxFilter.module.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useTranslation } from 'react-i18next';

export const AdsRatingFilter = props => {
  const { rating } = props;
  const { t } = useTranslation();
  const [sort, setSort] = useState('by listing date');
  const onClickSort = (opt)=>{
      setSort(opt);
      props.setSort(opt);
  }
  return (
    <div id={styles.ad_rating_container}>
      <div className={styles.ad_rating_items}>
        <span className={styles.ad_rating_item_large}>
          <BitOlxText text={`${rating} ${t('listings')}`} />
        </span>
        <span className={styles.ad_rating_item_small}>
          <BitOlxText text={`${rating} ${t('listings')}`} />
        </span>
      </div>
      <Dropdown>
        <Dropdown.Toggle className={styles.reset_button}>
          <span>{t(sort)}</span>
        </Dropdown.Toggle>

        <Dropdown.Menu
          className={
            props.styled
              ? stylesFilter.item_right_main
              : stylesFilter.item_right
          }
        >
          {filterData.data.options.map((option, index) => (
            <Dropdown.Item
              className={stylesFilter.dropdown_item}
              eventKey={`${index}`}
              onClick={()=>onClickSort(option)}
            >
              {t(option)}
            </Dropdown.Item>
          ))}
        </Dropdown.Menu>
      </Dropdown>
    </div>
  );
};

export default AdsRatingFilter;
