import 'mapbox-gl/dist/mapbox-gl.css';
import 'react-map-gl-geocoder/dist/mapbox-gl-geocoder.css';
import React, { Component } from 'react';
import MapGL, { NavigationControl, Layer } from 'react-map-gl';
import Geocoder from 'react-map-gl-geocoder';
import Pins from '../Products/pins';
import config from "../../config.json";

const MAPBOX_TOKEN = config.MapboxAccessToken;

const navStyle = {
  position: 'absolute',
  top: 0,
  right: 0,
  padding: '10px',
};

const paintLayer = {
  'fill-extrusion-color': '#aaa',
  'fill-extrusion-height': {
    type: 'identity',
    property: 'height',
  },
  'fill-extrusion-base': {
    type: 'identity',
    property: 'min_height',
  },
  'fill-extrusion-opacity': 0.6,
};

export default class Geo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      viewport: {
        latitude: props.latitude || 50.4501,
        longitude: props.longitude || 30.5234,
        zoom: 12,
      },
      location: null,
    };
  }

  mapRef = React.createRef();

  handleViewportChange = viewport => {
    console.log('VIEWPORT', viewport);
    this.setState({
      viewport: { ...this.state.viewport, ...viewport },
    }, () => this.props.onChange(viewport.latitude, viewport.longitude));
    
  };

  _updateViewport = viewport => {
    this.setState({ viewport });
  };

  handleGeocoderViewportChange = viewport => {
    const geocoderDefaultOverrides = { transitionDuration: 1000 };
    return this.handleViewportChange({
      ...viewport,
      ...geocoderDefaultOverrides,
    });
  };

  handleOnResult = event => {
    this.setState({
      viewport: {
        latitude: event.result.geometry.coordinates[1],
        longitude: event.result.geometry.coordinates[0],
        zoom: 15,
      },
    }, () => this.props.onChange(event.result.geometry.coordinates[1], event.result.geometry.coordinates[0]));
  };

  componentDidMount() {
    // window.setInterval(() => {
    //   this.props.onChange(
    //     this.state.viewport.latitude,
    //     this.state.viewport.longitude
    //   );
    // }, 2000);
  }

  render() {
    const { viewport } = this.state;
    return (
      <div style={{ height: '300px' }}>
        <MapGL
          ref={this.mapRef}
          {...viewport}
          width="100%"
          height="100%"
          onViewportChange={this.handleViewportChange}
          mapboxApiAccessToken={MAPBOX_TOKEN}
        >
          <Layer
            id="3d-buildings"
            type="fill-extrusion"
            source="composite"
            source-layer="building"
            paint={paintLayer}
            filter={['==', 'extrude', 'true']}
            minzoom={14}
          />
          <div className="nav" style={navStyle}>
            <NavigationControl
              showCompass={false}
              onViewportChange={this._updateViewport}
            />
          </div>
          <Geocoder
            mapRef={this.mapRef}
            onResult={this.handleOnResult.bind(this)}
            onViewportChange={this.handleGeocoderViewportChange}
            mapboxApiAccessToken={MAPBOX_TOKEN}
            position="top-left"
          />
          <Pins data={viewport} />
        </MapGL>
      </div>
    );
  }
}
