import React, { Component } from 'react';
import windowSize from 'react-window-size';
import 'mapbox-gl/dist/mapbox-gl.css';
import { ProductComponent } from '../../containers';
import styles from './BigMap.module.scss';
import { BitOlxTabContainer, BitOlxTab } from '../../commons';
import { faBars, faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons';
import { withTranslation, useTranslation } from 'react-i18next';
import MapComponent from './Map';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from "material-ui/Button/Button";
import { read } from "../product/api-product";
import Product from "../Products/Product";
import { withStyles } from '@material-ui/core/styles';
import ProductDrawer from "./productDrawer";
import config from "../../config.json";
import { fetchFilters, deleteQuery } from '../../utils/filters';
import NoSSR from 'react-no-ssr';
import NotificationSystem from 'react-notification-system';

const style = (theme) => ({
  '@global': {
  },
  appBar: {
    backgroundColor: '#7216f8',
  },
  fullList: {
    width: 'auto',
    height: '86vh',
    overflow: 'scroll',
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  paper: {
    height: 'calc(100% - 220px)',
    maxHeight: 'none',
    overflow: 'visible',
  },
  wrapper: {
    position: 'relative',
    top: -163,
    borderTopLeftRadius: 16,
    borderTopRightRadius: 16,
    background: 'white',
    visibility: 'visible !important',
  },
  container: {
    overflow: 'auto',
  },
});

export class Map extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchResultLayer: null,
      isDetailList: false,
      selectedStation: undefined,
      isList: false,
      isMap: true,
      queryObj: {},
      queryObjProps: {},
      openProductDetails: false,
      buttonTemplate: <></>,
      mapLoaded: false,
    };
    this._onBlur = this._onBlur.bind(this);
    this.deleteQuery = deleteQuery.bind(this);
  }


  notificationSystem = React.createRef();

  addNotification = text => {
    const notification = this.notificationSystem.current;
    notification.addNotification({
      message: text,
      level: 'success'
    });
  };

  _onBlur() {
    setTimeout(() => {
      console.log('blurred');
    }, 0);
  }


  onListSelected = () => {
    const { isList, viewport } = this.state;
    if (!isList) {
      this.setState({
        isList: true,
        isMap: false,
        viewport: {
          ...viewport,
          width: '0vh',
        },
      });
    }
  };

  onMapSelected = () => {
    const { isMap, viewport } = this.state;
    if (!isMap) {
      this.setState({
        isList: false,
        isMap: true,
        viewport: {
          ...viewport,
          width: '100vh',
        },
      });
    }
  };

  componentDidMount() {
    let url_string = window.location.href; //window.location.href
    let url = new URL(url_string);
    let signedup = url.searchParams.get("signedup");
    let loggedin = url.searchParams.get("loggedin");
    if (signedup) {
      this.addNotification('   :)');
    }
    else if (loggedin) {
      this.addNotification('  :$');
    }
    this.setState({
      queryObj: {},
    });
  }

  handleSelect = (ids) => {
    if (this.props.windowWidth > 760)
      this.refs.products_component.triggerProductLoad(ids);
    else
      this.setState({ productTemplate: [] }, () => {
        ids.map((id, index) => {
          read({ productId: id }).then((data) => {
            index === 0 ? (
              this.setState({ buttonTemplate: <Button onClick={() => window.location.href = `${config.apiURL}/apartment/` + data.name + '/' + data._id} variant="contained" className={styles.product_drawer_button} >{data.name}</Button> }, () => console.log(data))
            ) : (
                void (0)
              );
            this.setState({ productTemplate: [...this.state.productTemplate, this.productJSXTemplate(data, index)] }, () => this.setState({ showDrawer: true }))
          })
        })
      })
  };

  handleAllTrigger = () => {
    this.props.windowWidth > 760 ? this.refs.products_component.triggerAllProducts() : this.setState({ showDrawer: false })
  };

  toggleDrawer = (event, open) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    this.setState({ openProductDetails: open });
  };

  mapLoaded = () => {
    this.setState({ mapLoaded: true })
  }

  mapNotLoaded = () => {
    this.setState({ mapLoaded: false })
  }

  productJSXTemplate(template, index) {
    return (
      <div style={{ margin: "8px" }} key={`${index}`} >
        <Product
          oneRow={this.props.oneRow}
          key={template._id}
          template={template}
        />
      </div>
    );
  };

  hideNavbar = () => {
    const navbar = document.getElementById("navbar");
    navbar ? navbar.style.display = "none" : void (0)
    return null;
  }

  showNavbar = () => {
    const navbar = document.getElementById("navbar");
    navbar ? navbar.style.display = "inherit" : void (0)
    return null;
  }


  render() {
    try {
      window.customerly ? window.customerly("hide") : void (0);
    }
    catch (e) {
      void (0);
    }
    const { isList, isMap } = this.state;
    const { windowWidth, classes, t } = this.props;

    return (
      <div id={styles.big_map_container}>
        <NotificationSystem ref={this.notificationSystem} />
        <NoSSR>
          <div className="flex">
            {windowWidth > 760 ? (
              <>
                <div className={styles.listings_map} ref={(ref) => this.scrollParentRef = ref}>
                  <ProductComponent
                    queryObjProps={this.state.queryObjProps}
                    onChange={fetchFilters.bind(this)}
                    ref="products_component"
                    scrollRef={this.scrollParentRef}
                    mapLoaded={this.state.mapLoaded}
                    mapNotLoaded={this.mapNotLoaded.bind(this)}
                    oneRow
                  />
                </div>
                <MapComponent
                  onAllTrigger={this.handleAllTrigger}
                  queryObjProps={this.state.queryObjProps}
                  onSelect={this.handleSelect}
                  t={this.props.t}
                  windowWidth={this.props.windowWidth}
                  isMap={this.state.isMap}
                  isDetailList={this.state.isDetailList}
                  mapLoaded={this.mapLoaded.bind(this)}
                />
              </>
            ) : (
                <>
                  <div className={styles.listings_map} ref={(ref) => this.scrollParentRef = ref}>
                    {isList &&
                      <>
                        {this.showNavbar()}
                        <ProductComponent
                          queryObjProps={this.state.queryObjProps}
                          onChange={fetchFilters.bind(this)}
                          ref="products_component"
                          scrollRef={this.scrollParentRef}
                          oneRow
                        />
                      </>
                    }
                    {isMap && (
                      <>
                        {this.hideNavbar()}
                        <MapComponent
                          onAllTrigger={this.handleAllTrigger}
                          queryObjProps={this.state.queryObjProps}
                          onSelect={this.handleSelect}
                          t={this.props.t}
                          isMap={this.state.isMap}
                          isDetailList={this.state.isDetailList}
                          windowWidth={this.props.windowWidth}
                          mapLoaded={this.mapLoaded.bind(this)}
                        />
                        {
                          this.state.showDrawer &&
                          <React.Fragment>
                            <SwipeableDrawer
                              anchor={'bottom'}
                              open={this.state.openProductDetails}
                              onClose={(e) => this.toggleDrawer(e, false)}
                              onOpen={(e) => this.toggleDrawer(e, true)}
                              ModalProps={{
                                keepMounted: true,
                              }}
                              disableSwipeToOpen={false}
                              swipeAreaWidth={163}
                              BackdropProps={{
                                invisible: true,
                              }}
                              classes={{
                                paper: classes.paper,
                              }}
                            >

                              <div className={classes.wrapper}>
                                {this.state.buttonTemplate}
                                <div className={classes.container}>
                                  <div
                                    className={classes.fullList}
                                    role="presentation"
                                    onClick={(e) => this.toggleDrawer(e, false)}
                                    onKeyDown={e => this.toggleDrawer(e, false)}

                                  >
                                    <ProductDrawer productTemplate={this.state.productTemplate} />
                                  </div>
                                </div>

                              </div>

                            </SwipeableDrawer>
                          </React.Fragment>
                        }
                      </>
                    )}
                  </div>
                </>
              )}
            <div className={styles.bottom_tab_container}>
              <BitOlxTabContainer>
                <BitOlxTab
                  iconName={faBars}
                  iconTitle={t('List')}
                  isSelected={isList}
                  onMenuItemClicked={this.onListSelected}
                />
                <BitOlxTab
                  iconName={faMapMarkerAlt}
                  iconTitle={t('Map')}
                  isSelected={isMap}
                  onMenuItemClicked={this.onMapSelected}
                />
              </BitOlxTabContainer>
            </div>
          </div>
        </NoSSR>
      </div>
    );
  }
}

const NamespaceComponent = withStyles(style)(withTranslation()(Map));
export default windowSize(NamespaceComponent);
