import 'react-responsive-carousel/lib/styles/carousel.min.css';
import './index.css';
import './i18n';

const DEFAULT_CONFIG = {
  trackingId: 'UA-161627233-1',
  debug: true,
  gaOptions: {
    cookieDomain: 'citvy.com',
  },
};

ReactGA.initialize([DEFAULT_CONFIG]);
ReactGA.pageview('init-pageview');

hydrate(<BrowserRouter><App /></BrowserRouter>, document.getElementById('root'));

