import React from 'react';
import styles from './BitOlxTab.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const BitOlxTab = props => {
  const { iconName, iconTitle, isSelected, onMenuItemClicked } = props;
  const colorStyle = isSelected ? styles.select_style : styles.unselected_style;
  return (
    <div id={styles.tab_item_container} onClick={onMenuItemClicked}>
      <div className={styles.icon_div_style}>
        <FontAwesomeIcon
          className={`${styles.tab_search_icon} ${colorStyle}`}
          icon={iconName}
        />
      </div>
      <div className={styles.icon_text_style}>
        <span className={`${styles.tab_text} ${colorStyle}`}>{iconTitle}</span>
      </div>
    </div>
  );
};

export default BitOlxTab;
