import React, { Component } from 'react';
import auth from '../../auth/auth-helper';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Icon from 'material-ui/Icon';
import Avatar from 'material-ui/Avatar';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { read, update } from './api-product.js';
import { Link, Redirect } from 'react-router-dom';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import { BitOlxButton } from '../../commons';
import decrypt from './simple_decryptor';
import Geo from './Geo';
import { withTranslation } from 'react-i18next';

const styles = theme => ({
  card: {
    margin: 'auto',
    textAlign: 'center',
    marginTop: theme.spacing.unit * 3,
    marginBottom: theme.spacing.unit * 2,
    maxWidth: 900,
    paddingBottom: theme.spacing.unit * 2,
  },
  title: {
    margin: theme.spacing.unit * 2,
    color: theme.palette.protectedTitle,
    fontSize: '1.2em',
  },
  error: {
    verticalAlign: 'middle',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: '100%',
  },
  submit: {
    margin: 'auto',
    marginBottom: theme.spacing.unit * 2,
    textDecoration: 'none',
    width: '150px!important',
  },
  bigAvatar: {
    width: 140,
    height: 140,
    margin: 'auto',
  },
  input: {
    display: 'none',
  },
  filename: {
    marginLeft: '10px',
  },
});

class EditProduct extends Component {
  constructor({ match }) {
    super();
    this.state = {
      name: '',
      description: '',
      images: [],
      category: '',
      price: '',
      telephone: '',
      floor: '',
      area: '',
      kitchenArea: '',
      livingArea: '',
      publisher: 'owner',
      redirect: false,
      error: '',
    };
    this.handleFieldChange = this.handleFieldChange.bind(this);
    this.match = match;
  }

  handleFieldChange(latitude, longitude) {
    // let lt = latitude;
    this.setState({
      location: {
        latitude: latitude,
        longitude: longitude,
      },
    });
  }

  componentDidMount = () => {
    this.productData = new FormData();
    read({
      productId: this.match.params.productId,
    }).then(data => {
      if (data.error) {
        this.setState({ error: data.error });
      } else {
        if (!data.location) {
          data.location = { longitude: -74.00260979981, latitude: 40.7350952 };
        }
        this.setState({
          id: data._id,
          name: data.name,
          description: data.description,
          category: data.category,
          price: data.price,
          images: data.images,
          telephone: decrypt(data.telephone),
          floor: data.floor,
          area: data.area,
          kitchenArea: data.kitchenArea,
          livingArea: data.livingArea,
          publisher: data.publisher,
          location: data.location,
          address: data.location.address,
        });
      }
    });

    let letterLimitTitle = 70;
    let letterLimit = 9000;

    let textareaTitle = document.getElementById('name');
    let textarea = document.getElementById('multiline-flexible');

    document.getElementById('lettersCountTitle').innerHTML = letterLimitTitle;
    document.getElementById('lettersCount').innerHTML = letterLimit;

    textareaTitle.addEventListener(
      'input',
      function () {
        document.getElementById('lettersCountTitle').innerHTML =
          letterLimitTitle - textareaTitle.value.length;
      },
      false
    );
    textarea.addEventListener(
      'input',
      function () {
        document.getElementById('lettersCount').innerHTML =
          letterLimit - textarea.value.length;
      },
      false
    );
  };

  clickSubmit = () => {
    function stripScripts(s) {
      var div = document.createElement('div');
      div.innerHTML = s;
      var scripts = div.getElementsByTagName('script');
      var i = scripts.length;
      while (i--) {
        scripts[i].parentNode.removeChild(scripts[i]);
      }
      return div.innerHTML;
    }
    let images = this.state.images;
    let finalImages = [];
    var imageExists = image_url => {
      let http = new XMLHttpRequest();

      http.open('HEAD', image_url, false);
      http.send();
      if (http.status === 200) {
        finalImages.push(image_url);
      }
    };

    let imgsLength = images.length;

    for (var i = 0; i < imgsLength; i++) {
      imageExists(images[i]);
    }

    const product = {
      name: this.state.name || undefined,
      description: stripScripts(this.state.description) || undefined,
      images: finalImages || undefined,
      category: this.state.category || undefined,
      price: this.state.price || undefined,
      telephone: this.state.telephone || undefined,
      floor: this.state.floor || undefined,
      area: this.state.area || undefined,
      kitchenArea: this.state.kitchenArea || undefined,
      livingArea: this.state.livingArea || undefined,
      publisher: this.state.publisher || undefined,
      location: {
        longitude: this.state.location.longitude,
        latitude: this.state.location.latitude,
        address: this.state.address,
      },
    };

    if (finalImages.length < 3) {
      this.setState({ error: '  3- ' });
    } else if (this.state.description.length < 40) {
      this.setState({ error: '  ' });
    } else if (this.state.description.length > 9000) {
      this.setState({
        error: '  ',
      });
    } else if (this.state.name.length > 70) {
      this.setState({
        error: '  ',
      });
    } else if (this.state.price.length > 50) {
      this.setState({
        error: '  ',
      });
    } else {
      const jwt = auth.isAuthenticated();
      for (i = 0; i < 100; i++) {
        window.clearInterval(i);
      }
      update(
        {
          ownerId: jwt.user._id,
          productId: this.match.params.productId,
        },
        {
          t: jwt.token,
        },
        product
      ).then(data => {
        if (data.error) {
          this.setState({ error: data.error });
        } else {
          this.setState({ redirect: true });
        }
      });
    }
  };

  checkUploadResult = resultEvent => {
    this.setState({
      images: [...this.state.images, resultEvent.info.secure_url],
    });
  };

  showWidget = widget => {
    widget.open();
  };

  handleChange = name => event => {
    this.setState({ [name]: event.target.value });
  };

  render() {
    const imgItems = [];
    const { t } = this.props;
    let widget = window.cloudinary.createUploadWidget(
      {
        cloudName: 'bitolx',
        uploadPreset: 'ml_default',
        maxFiles: 40,
        theme: 'minimal',
        thumbnails: '.thumbs',
      },
      (error, result) => {
        if (!error && result && result.event === 'success') {
          this.checkUploadResult(result);
        }
      }
    );

    for (const [index, value] of this.state.images.entries()) {
      imgItems.push(
        <div className="thumbs-edit">
          <img
            alt="Preview to edit"
            className="preview-imgs-edit"
            src={value}
            key={index}
          ></img>
          <div
            onClick={() => {
              this.setState({
                images: this.state.images.filter(e => e !== value),
              });
            }}
            className="delete-button"
          >
            {t('Delete')}
          </div>
        </div>
      );
    }

    if (this.state.redirect) {
      return <Redirect to="/products/my/id" />;
    }
    const { classes } = this.props;
    try {
      window.customerly ? window.customerly("hide") : void (0);
    }
    catch (e) {
      void (0);
    }
    return (
      <div>
        <Card className={classes.card}>
          <CardContent>
            <div type="headline" component="h2" className={classes.title}>
              {t('Edit Product')}
            </div>
            <Avatar src={this.state.images[0]} className={classes.bigAvatar} />
            <div className="position">
              <div className="position_1">{t('Title')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="name"
                  className={classes.textField}
                  value={this.state.name}
                  onChange={this.handleChange('name')}
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="help-text-title">
              <div id="lettersCountTitle"></div>&nbsp;{t('characters left')}
            </div>
            <div className="position">
              <div className="position_1">{t('Description')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="multiline-flexible"
                  multiline
                  rows="8"
                  value={this.state.description}
                  onChange={this.handleChange('description')}
                  className={classes.textField}
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <div className="help-text-desc">
              <div id="lettersCount"></div>&nbsp;
              {t('characters left, HTML5 supported')}
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Price in USD')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="price"
                  className={classes.textField}
                  value={this.state.price}
                  onChange={this.handleChange('price')}
                  type="text"
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Telophone')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="Telephone"
                  placeholder="380XXXXXXXXXX"
                  className={classes.textField}
                  value={this.state.telephone}
                  onChange={this.handleChange('telephone')}
                  type="number"
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Floor')} *</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="select_Floor"
                  placeholder="2"
                  className={classes.textField}
                  value={this.state.floor}
                  onChange={this.handleChange('floor')}
                  type="text"
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Area')}*</div>
              <div className="position_2 margin_iss">
                <TextField
                  id="select_Area"
                  placeholder="60"
                  className={classes.textField}
                  value={this.state.area}
                  onChange={this.handleChange('area')}
                  type="text"
                  margin="normal"
                  variant="outlined"
                />
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">
                {t('Kitchen/Living Area')} *
              </div>
              <div className="position_2 margin_iss">
                <div className="flex">
                  <TextField
                    id="select_LivingArea"
                    placeholder="30"
                    className={classes.textField}
                    value={this.state.livingArea}
                    onChange={this.handleChange('livingArea')}
                    type="text"
                    margin="normal"
                    variant="outlined"
                  />
                  <TextField
                    id="select_KitchenArea"
                    placeholder="15"
                    className={classes.textField}
                    value={this.state.kitchenArea}
                    onChange={this.handleChange('kitchenArea')}
                    type="text"
                    margin="normal"
                    variant="outlined"
                  />
                </div>
              </div>
            </div>
            <br />
            <div className="position">
              <div className="position_1">{t('Publisher')} *</div>
              <div className="position_2">
                <Select
                  name="Publisher"
                  id="select_Interm"
                  onChange={this.handleChange('publisher')}
                  value={this.state.publisher}
                  variant="outlined"
                  defaultValue="owner"
                  className="position_3"
                >
                  <MenuItem value="owner">{t('owner')}</MenuItem>
                  <MenuItem value="broker">{t('broker')}</MenuItem>
                </Select>
              </div>
            </div>
            <br />
            <div className="category-select-text">{t('Edit Images')}</div>
            <button
              onClick={() => this.showWidget(widget)}
              className="cloudinary-button"
            >
              {t('Upload Photos')}
            </button>
            <div className="img-items">{imgItems}</div>
            <br />
            <div>{t('Enter the address into the search box')}</div>
            {this.state.location && (
              <Geo
                onChange={this.handleFieldChange}
                latitude={parseFloat(this.state.location.latitude) || 40}
                longitude={parseFloat(this.state.location.longitude) || -71}
              />
            )}
            {this.state.error && (
              <Typography component="p" color="error">
                <Icon color="error" className={classes.error}>
                  Error&nbssp;
                </Icon>
                {this.state.error}
              </Typography>
            )}
          </CardContent>
          <CardActions>
            <BitOlxButton
              fullWidth
              title={t('Update')}
              onClick={this.clickSubmit}
            />
            <Link to="/" style={{ width: '100%' }}>
              <BitOlxButton fullWidth title={t('Cancel')} />
            </Link>
          </CardActions>
        </Card>
      </div>
    );
  }
}

EditProduct.propTypes = {
  classes: PropTypes.object.isRequired,
};

const NamespaceComponent = withTranslation()(EditProduct);
export default withStyles(styles)(NamespaceComponent);
