import React, { Component } from 'react';
import PropTypes from 'prop-types';
import DeleteIcon from 'material-ui-icons/Delete';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';
import auth from '../../auth/auth-helper';
import { remove } from './api-product.js';
import { withTranslation } from 'react-i18next';
import { BitOlxButton, BitOlxIconButton } from '../../commons';
import Edit from 'material-ui-icons/Edit';
import { Link } from 'react-router-dom';

class DeleteProduct extends Component {
  state = {
    open: false,
  };
  clickButton = () => {
    this.setState({ open: true });
  };
  deleteProduct = () => {
    console.log(this.props);
    const jwt = auth.isAuthenticated();
    remove(
      {
        ownerId: this.props.product.owner._id,
        productId: this.props.product._id,
      },
      { t: jwt.token }
    ).then(data => {
      if (data.error) {
        console.log(data.error);
      } else {
        this.setState({ open: false }, () => {
          this.props.onRemove(this.props.product);
        });
      }
    });
  };
  handleRequestClose = () => {
    this.setState({ open: false });
  };
  render() {
    const { t } = this.props;
    return (
      <span>
        <BitOlxIconButton icon={<DeleteIcon />} onClick={this.clickButton} />
        <Dialog open={this.state.open} onClose={this.handleRequestClose}>
          <DialogTitle>
            {'?'}
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              {t('Confirm to delete your product')} {this.props.product.name}.
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <BitOlxButton
              title={t('Cancel')}
              onClick={this.handleRequestClose}
              fullWidth
            />
            <BitOlxButton
              fullWidth
              title={''}
              onClick={this.deleteProduct}
            />
          </DialogActions>
        </Dialog>
      </span>
    );
  }
}
DeleteProduct.propTypes = {
  product: PropTypes.object.isRequired,
  onRemove: PropTypes.func.isRequired,
};
export default withTranslation()(DeleteProduct);
