import config from "../config.json";

function deleteQuery(queryObj, type) {
    const type_attr_dict = {
        Price: 'price',
        Area: 'area',
        Floor: 'floor',
        Publisher: 'publisher',
        Kitchen: 'kitchenArea',
        Dwelling: 'livingArea',
        Date: 'created',
    };
    if (
        (Object.keys(queryObj).length === 0) &&
        this.state.queryObj.hasOwnProperty(type_attr_dict[type])
    ) {
        let queryObjTemp = this.state.queryObj;
        delete queryObjTemp[type_attr_dict[type]];
        this.setState({ queryObj: queryObjTemp });
    }
    return Promise.resolve(true);
}

function fetchFilters(type, value) {
    let queryObj;
    queryObj = {};
    const type_attr_dict = {
        Price: 'price',
        Area: 'area',
        Floor: 'floor',
        Publisher: 'publisher',
        Kitchen: 'kitchenArea',
        Dwelling: 'livingArea',
        Date: 'created',
    };
    let queryObjToAdd = {};
    if (value === 'any' || value === 'anys' || value.length === 0) {
        queryObjToAdd = {};
    } else if (type === 'Price') {
        if (typeof value === 'string') {
            value = {
                min: '0',
                max: value.replace('up to $', '').replace(/,/g, ''),
            };
        }
        queryObjToAdd = { price: { $gte: value.min, $lte: value.max } };
    } else if (
        type === 'Area' ||
        type === 'Floor' ||
        type === 'Kitchen' ||
        type === 'Dwelling'
    ) {
        if (typeof value === 'string') {
            console.log(value, value.match(/\d+/)[0]);
            value = {
                min: Number(value.match(/\d+/)[0]),
                max: 999999999,
            };
        }
        queryObjToAdd = {
            [type_attr_dict[type]]: { $gte: value.min, $lte: value.max },
        };
        console.log(queryObjToAdd);
    } else if (type === 'Rooms') {
        console.log(value);
        if (typeof value === 'string') {
            value = {
                min: value.match(/\d+/)[0], //The parameters seem to be taking the inverse values
                max: value.match(/\d+/)[0],
            };
            if (value == '5+')
                value = {
                    min: 5,
                    max: 99999,
                };
        }
        queryObjToAdd = {
            rooms: { $gte: value.min, $lte: value.max },
        };
    }
    else if (type === 'Date') {
        let today = new Date();
        let lowerLimit = new Date('October 15, 1996 05:35:32');
        if (value === 'today') {
            let midnight = new Date();
            midnight.setUTCHours(0, 0, 0, 0);
            value = {
                min: midnight.toISOString(),
                max: today.toISOString(),
            };
        } else if (value === 'today or yesterday') {
            let yesterday = new Date(today.getTime() - 2 * 24 * 60 * 60 * 1000);
            yesterday.setUTCHours(24, 0, 0, 0);
            value = {
                min: yesterday.toISOString(),
                max: today.toISOString(),
            };
        } else if (value === 'last 3 days') {
            let three = new Date(today.getTime() - 3 * 24 * 60 * 60 * 1000);
            three.setUTCHours(0, 0, 0, 0);
            value = {
                min: three.toISOString(),
                max: today.toISOString(),
            };
        } else if (value === 'last week') {
            let week = new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000);
            week.setUTCHours(0, 0, 0, 0);
            value = {
                min: week.toISOString(),
                max: today.toISOString(),
            };
        }
        else if (value === 'last month') {
            let month = new Date(today.getTime() - 31 * 24 * 60 * 60 * 1000);
            month.setUTCHours(0, 0, 0, 0);
            value = {
                min: month.toISOString(),
                max: today.toISOString(),
            };
        }
        else {
            value = {
                min: lowerLimit.toISOString(),
                max: today.toISOString(),
            };
        }
        queryObjToAdd = { created: { $gte: value.min, $lte: value.max } };
    } else if (type === 'Publisher') {
        queryObjToAdd = { publisher: value }
    }
    else if (type === 'Category') {
        this.setState({ queryObj: {} })
        queryObjToAdd = { "otherObjects.rubric": value };
    }
    
    this.deleteQuery(queryObjToAdd, type).then(() => {
        this.setState(
            { queryObj: { ...this.state.queryObj, ...queryObjToAdd } },
            () => {
                this.setState({ queryObjProps: this.state.queryObj })
            }
        );
    });
    console.log('state obj:', this.state.queryObj);
}

export { fetchFilters, deleteQuery };