import React, { useState, useEffect, useRef } from 'react';
import MapGL, { NavigationControl, Layer, Source, Marker as GLMarker } from 'react-map-gl';
import styles from './BigMap.module.scss';
import uniqBy from 'lodash.uniqby';
import config from "../../config.json";
import MapboxLanguage from "@mapbox/mapbox-gl-language";
import Geocoder from 'react-map-gl-geocoder';
import { ReactComponent as MapMaker } from "../../assets/img/icons/map-icon.svg";

const MAPBOX_TOKEN = config.MapboxAccessToken;

const MARKER_SIZE = 30;

const paintLayer = {
  'fill-extrusion-color': '#aaa',
  'fill-extrusion-height': {
    type: 'identity',
    property: 'height',
  },
  'fill-extrusion-base': {
    type: 'identity',
    property: 'min_height',
  },
  'fill-extrusion-opacity': 0.6,
};

const Map = ({ t, windowWidth, onAllTrigger, onSelect, isMap, isDetailList, queryObjProps, mapLoaded }) => {
  const mapRef = useRef(null);
  const [viewport, setViewport] = useState({});
  const [markers, setMarkers] = useState({
    type: 'FeatureCollection',
    features: []
  });
  // const [selected, setSelected] = useState('');
  const [popupVisible, setPopupVisible] = useState(false);
  const [popupProps, setPopupProps] = useState({})
  useEffect(() => {
    setViewport({
      latitude: 50.4501,
      longitude: 30.5234,
      width: windowWidth > 760 ? 'calc(100vw - 400px)' : '100vh',
      minWidth: '60px',
      height: windowWidth > 760 ? 'calc(100vh - 40px)' : '100vh',
      zoom: 12,
    });
    setTimeout(() => {
      if (mapRef.current) {
        const map = mapRef.current.getMap();
        map.getStyle().layers.forEach(function (thisLayer) {
          if (thisLayer.type == 'symbol') {
            map.setLayoutProperty(thisLayer.id, 'text-field', ['get', 'name_ru'])
          }
        })
      }
    }, 2000);
    fetch(`${config.apiURL}/bitolx/api/products/map`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(queryObjProps),
    })
      .then(response => response.json())
      .then(data => {
        setMarkers({
          type: "FeatureCollection",
          features: uniqBy(
            data.products,
            ({ location: { latitude, longitude } }) => `${latitude}_${longitude}`
          ).map((loc) => ({
            type: "Feature",
            properties: { id: loc._id },
            geometry: { type: 'Point', coordinates: [loc.location.longitude, loc.location.latitude] }
          }))
        })
        mapLoaded()
      });

  }, [queryObjProps, windowWidth]);

  const handleResult = event => {
    setViewport({
      latitude: event.result.geometry.coordinates[1],
      longitude: event.result.geometry.coordinates[0],
      zoom: 12,
      width: 'calc(100vw - 400px)',
      minWidth: '60px',
      height: '100vh',
    });
  };

  const handleViewportChange = viewport => {
    setViewport(v => ({ ...v, ...viewport }));
  };

  const handleGeocoderViewportChange = viewport => {
    return handleViewportChange({
      ...viewport,
      transitionDuration: 1000,
    });
  };

  const handleMapClick = (e) => {
    if (e.features.length === 0) {
      onAllTrigger ? onAllTrigger() : void (0);
      setPopupVisible(false);
    }
    else {
      onSelect(e.features.map((obj) => obj.properties.id));
      setPopupProps({ longitude: e.features[0].geometry.coordinates[0], latitude: e.features[0].geometry.coordinates[1] });
      setPopupVisible(true);
    }
  };

  return (
    <MapGL
      className={`${!isMap || isDetailList ? styles.big_map_gl : ''} `}
      ref={mapRef}
      {...viewport}
      mapStyle="mapbox://styles/mapbox/streets-v11"
      onViewportChange={handleViewportChange}
      mapboxApiAccessToken={MAPBOX_TOKEN}
      interactiveLayerIds={["point"]}
      onClick={handleMapClick}
    >
      <Layer
        id="3d-buildings"
        type="fill-extrusion"
        source="composite"
        source-layer="building"
        paint={paintLayer}
        filter={['==', 'extrude', 'true']}
        minzoom={14}
      />
      <Source type="geojson" data={markers}>
        <Layer
          id="point"
          type="circle"
          paint={{
            'circle-radius': [
              "interpolate", ["linear"], ["zoom"],
              14, 4,
              15, 7
            ],
            'circle-color': 'black',
            'circle-stroke-color': 'white',
            'circle-stroke-width': 0.5,
          }} />
      </Source>
      {popupVisible &&
        <div
          className="station"
          style={{
            width: '0px',
            transform: `translate(${-MARKER_SIZE * 0.5}px,${-MARKER_SIZE}px)`,
          }}
        >
          <GLMarker
            longitude={popupProps.longitude}
            latitude={popupProps.latitude}
          >
            <svg
              height={MARKER_SIZE}
              viewBox="0 0 24 24"
              style={{
                cursor: 'pointer',
                fill: 'black',
                stroke: 'none',
              }}
            >
              <MapMaker />
            </svg>
          </GLMarker>
        </div>
      }
      {windowWidth > 760 && <Geocoder
        mapRef={mapRef}
        placeholder={t('')}
        onResult={handleResult}
        onViewportChange={handleGeocoderViewportChange}
        mapboxApiAccessToken={MAPBOX_TOKEN}
        position="top-left"
      />}
      <div className="nav">
        <NavigationControl
          showCompass={false}
          onViewportChange={setViewport}
        />
      </div>
    </MapGL>
  );
};

export default React.memo(Map);
