import React, { Component } from 'react';
import InfiniteScroll from 'react-infinite-scroller';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { listByOwner, read } from '../product/api-product.js';
import './AllProducts.scss';
import { withTranslation } from 'react-i18next';
import Product from './Product';
import { listView } from "../product/api-product";
import SimpleBackdrop from "./Backdrop";
import { listByLord } from '../product/api-product.js';
import Switch from 'react-switch';

class AllProducts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      page: 0,
      sortKey: 'created',
      count: 1,
      _id: '',
      isMap: false,
      productsLoaded: false,
      checked: false
    };

    this.productJSXTemplate = this.productJSXTemplate.bind(this);
  }

  productJSXTemplate(template) {
    return (
      <Product
        oneRow={this.props.oneRow}
        key={template._id}
        template={template}
      />
    );
  }

  componentDidMount() {
    this.loadProducts();
  }

  loadOneProduct(ids) {
    this.setState({ products: [] }, () => {
      ids.map((_id) => {
        read({ productId: _id }).then(data => {
          if (data && data.error) {
            console.log(data.error);
          } else {
            this.setState({ products: [...this.state.products, this.productJSXTemplate(data)], _id: _id });
          }
        });
      })
    })
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.queryObjProps !== this.props.queryObjProps || prevProps.ordering !== this.props.ordering) {
      this.setState({ page: 0, products: [], productsLoaded: false }, () => {
        this.loadProducts("queryChange");
      })
    }
  }

  loadProducts = (callee = "none") => {
    if (this.state._id) {
      this.setState({
        products: [],
        page: 0,
        sortKey: 'created',
        count: 1,
        _id: '',
      });
    }
    let ordering = this.props.ordering ? this.props.ordering : { sort: 'created', desc: true }
    window.location.href.includes('/user/')
      ? listByOwner(this.props.userId, this.state.page, true).then(data => {
        if (data && data.error) {
          console.log(data.error);
        } else {
          let newProducts = data.products;
          let veryNewProducts = newProducts.map((product, index) => {
            return this.productJSXTemplate(product, index);
          });
          this.setState({
            products: [...veryNewProducts],
            page: this.state.page + 1,
            count: data.count,
            productsLoaded: true,
          });
        }
      })
      : this.props.lord ? listByLord(this.props.lord, this.state.page).then(data => {
        if (data && data.error) {
          console.log(data.error);
        } else {
          if (data) {
            let newProducts = data.products;
            let veryNewProducts = newProducts.map((product, index) => {
              return this.productJSXTemplate(product.doc, index);
            });
            this.setState({
              products: [...veryNewProducts],
              page: this.state.page + 1,
              count: data.count,
              productsLoaded: true
            });
          }
        }
      })
        : listView(this.props.queryObjProps, this.state.page, ordering.sort, this.state.count, ordering.desc).then(data => {
          if (data && data.error) {
            console.log(data.error);
          } else {
            let newProducts = data.products;
            let veryNewProducts = newProducts.map((product, index) => {
              return this.productJSXTemplate(product, index);
            });
            if (callee === "queryChange") {
              this.setState({
                products: veryNewProducts,
                page: 1,
                count: data.count,
                productsLoaded: true,
              }, () => {
                if (this.props.setCount)
                  this.props.setCount(this.state.count)
                if (typeof (this.props.mapLoaded) !== 'undefined') {
                  this.props.mapNotLoaded()
                }
              });
            }
            else {
              this.setState(prevState => ({
                products: [...prevState.products, ...veryNewProducts],
                page: this.state.page + 1,
                count: data.count,
                productsLoaded: true,
              }), () => {
                if (this.props.setCount)
                  this.props.setCount(this.state.count)
              })
            }
          }
        });
  };

  render() {
    const { t } = this.props;
    if (this.props.css !== null && this.state.products.length && this.state.productsLoaded) {
      return (
        <main role="main" id="container" className="push">
          {this.state.count > 0 && (
            <section className="products">
              <div className="content">
                {!this.props.noLatest && (<div className="flexi-space">
                  <div className="flexy">
                    <div className="free-text">{t('Map')}</div>
                    <Switch
                      className="switch-box"
                      onColor="#e0e0e0"
                      offColor="#e0e0e0"
                      uncheckedIcon={false}
                      checked={this.state.checked}
                      checkedIcon={false}
                      onChange={() => {
                        this.setState({ checked: true })
                        window.location.href = '/map';
                      }}
                    />
                  </div>
                </div>
                )}

                <div className="all-product-list product-list">
                  {this.state._id && this.state._id.length > 0 ? (
                    <div>{this.state.products}</div>
                  ) : (
                      this.props.scrollRef ? (
                        <InfiniteScroll
                          loadMore={this.loadProducts.bind(this)}
                          hasMore={this.state.products.length < this.state.count}
                          threshold={200}
                          loader={
                            <div className="loader">
                              ...
                        </div>
                          }
                          useWindow={false}
                          getScrollParent={() => this.props.scrollRef}
                        >
                          {this.state.products}
                        </InfiniteScroll>
                      ) : (
                          <InfiniteScroll
                            loadMore={this.loadProducts.bind(this)}
                            hasMore={this.state.products.length < this.state.count}
                            threshold={200}
                            loader={
                              <div className="loader">
                                ...
                              </div>
                            }
                          >
                            {this.state.products}
                          </InfiniteScroll>
                        )
                    )}
                </div>
              </div>
            </section>
          )}
        </main>
      );
    }
    else if (this.state.productsLoaded && !this.state.products.length && this.props.css !== null)
      return (
        <main role="main" id="container" className="push">
          <section className="products">
            <div className="content">
              <p>{t('Try a different filter or Contact help.')}</p>
            </div>
          </section>
        </main>
      )

    return (
      <main role="main" id="container" className="push">
        <SimpleBackdrop />
        <section className="products">
          <div className="content">
            <p>{t('Products are being loaded...')}</p>
          </div>
        </section>
      </main>
    );
  }
}

export default withTranslation('translation', { withRef: true })(AllProducts);
