import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import Modal from 'react-modal';
import { withTranslation } from 'react-i18next';
import auth from '../../auth/auth-helper';
import config from '../../config';
import MaterialIcon from './material-icon'
import HeaderOptions from './header-options'
import logo from '../../assets/img/icons/logo.svg';
import './_header.scss';

Modal.setAppElement('#root');
function scrollToTop() {
  document.body.scrollTop = 0;
  document.documentElement.scrollTop = 0;
}
class HeaderN extends React.Component {
  constructor() {
    super();
    this.state = {
      showOptions: false
    }
  }
  componentDidMount() {
    if (this.props.user) {
      let user = JSON.stringify(this.props.user.email);
      localStorage.setItem('userD', user.substring(1, user.length - 1));
    }
  }

  toggleOptions = () => this.setState({ showOptions: !this.state.showOptions })

  render() {
    const { history } = this.props;
    const { t } = this.props;
    return (
      <div>
        <div className='nav-container'>
          <div className='primary-nav light'>
            <div className='logo-link'>
              {window.location.pathname == '/' ? (
                <div className='big-logo' aria-hidden='true' onClick={() => scrollToTop()}>
                  <object data={logo} type='image/svg+xml' style={{ pointerEvents: 'none' }} >
                    <span className='hide-content'>{t('Citvy')}</span>
                  </object>
                </div>
              ) : (
                  <Link to='/'>
                    <div className='big-logo' aria-hidden='true' >
                      <object data={logo} type='image/svg+xml' style={{ pointerEvents: 'none' }} >
                        <span className='hide-content'>{t('Citvy')}</span>
                      </object>
                    </div>
                  </Link>
                )}
            </div>
          </div>
          <div style={{ display: 'flex' }} className='right-block-nav'>
          <nav style={{ margin: '0 0 0 10px' }} className='secondary-nav light main-header-text'>
              <div>
                <a href={(localStorage.getItem('userD') || auth.isAuthenticated()) ? (config.socialURL + '/products/new') : (config.socialURL + '/signup')}> </a>
              </div>
            </nav>
            {(localStorage.getItem('userD') || auth.isAuthenticated()) && <nav style={{ margin: '0 0 0 10px' }} className='secondary-nav light'>
              <div>
                <a href={(localStorage.getItem('userD') || auth.isAuthenticated()) ? (config.socialURL + '/messages') : (config.socialURL + '/signup')}><MaterialIcon style={{ marginTop: '10px' }} icon='email' /> </a>
              </div>
            </nav>}
            {(localStorage.getItem('userD') || auth.isAuthenticated()) ? <nav style={{ margin: '0 0 0 10px' }} className='secondary-nav light'>
              <div>
                <Link to={'/cart'} ><MaterialIcon style={{ marginTop: '8px' }} icon='star_rate' /> </Link>
              </div>
            </nav> : <nav style={{ margin: '0 10px' }} className='secondary-nav light'>
                <div>
                  <Link to={'/cart'} ><MaterialIcon style={{ marginTop: '8px' }} icon='star_rate' /> </Link>
                </div>
              </nav>}
            {(localStorage.getItem('userD') || auth.isAuthenticated()) && < nav style={{ margin: '0 10px' }} className='secondary-nav light'>
              <div>
                <a href={config.socialURL + '/notifications'}><MaterialIcon style={{ marginTop: '8px' }} icon='notifications_none' /> </a>
              </div>
            </nav>}
            <a
              href={(localStorage.getItem('userD') || auth.isAuthenticated()) ? (config.socialURL + '/profile/' + auth.isAuthenticated().user.name) : (config.socialURL + `/signup`)}
              activeClassName='ha_active'
              className='sp'
            >
              <img src={(localStorage.getItem('userD') || auth.isAuthenticated()) ? (config.socialURL + '/users/' + auth.isAuthenticated().user.telegramId + '/avatar.jpg') : `https://s.citvy.com/users/23/avatar.jpg`} alt='avatar' className='profile-pic' />
              <span className='sp_span'></span>
            </a>
            <span className='show_more' onClick={this.toggleOptions}>
              <MaterialIcon icon='expand_more' style={{ color: 'white', marginTop: '9px' }} />
            </span>
            {this.state.showOptions ? (
              <HeaderOptions toggleOptions={this.toggleOptions} />
            ) : null}
          </div>
        </div>
      </div >
    );
  }
}

const HeaderNav = withRouter(HeaderN);
export default withTranslation()(HeaderNav);
