import React from 'react';
import { Switch, Route } from 'react-router-dom';
import SingleProductContainer from './Products/SingleProductContainer';
import Cart from './cart/Cart';
import BigMap from './BigMap/BigMap';
import { withTranslation } from 'react-i18next';
import withTracker from './withTracker';
import Header from './global/Header';
import Home from './Home/Home';
import NotFound from './global/NotFound';
import PrivateRoute from '../auth/PrivateRoute';
import SignUp from './global/SignUp';
import NewProduct from './product/NewProduct';
import EditProduct from './product/EditProduct';
import MyProducts from './product/MyProducts';
import LordProducts from './product/LordProducts';
import Users from './user/Users';
import EditProfile from './user/EditProfile';
import Profile from './user/Profile';
import Verification from './Products/Verification';
import Verification1 from './Products/Verification1';
import Verification3 from './Products/Verification3';
import Verification4 from './Products/Verification4';
import Verification5 from './Products/Verification5';
import Helmet from 'react-helmet';

class App extends React.Component {
  render() {
    return (
      <div>
        <Helmet>
          <title>    | CITVY</title>
          <meta name="description"
            content=" , ,         . CITVY          ." />
          <meta name="twitter:site" content="https://citvy.com/" />
          <meta name="twitter:title" content="    | CITVY" />
          <meta name="twitter:description"
            content=" , ,         . CITVY          ." />
        </Helmet>
        <Header />
        <Switch>
          <Route path="/" exact component={withTracker(Home)} />
          <Route path="/:board(\b\w{13,14}\b)" exact component={withTracker(LordProducts)} />
          <Route exact path="/home" component={withTracker(Home)} />
          <PrivateRoute exact path="/kyc" component={withTracker(Verification)} />
          <PrivateRoute exact path="/kyc1" component={withTracker(Verification1)} />

          <PrivateRoute exact path="/kyc3" component={withTracker(Verification3)} />
          <PrivateRoute exact path="/kyc4" component={withTracker(Verification4)} />
          <PrivateRoute exact path="/kyc5" component={withTracker(Verification5)} />

          <Route
            path="/apartment/:id"
            component={withTracker(SingleProductContainer)}
          />
          <Route path="/cart" component={withTracker(Cart)} />
          <Route
            path="/map"
            component={withTracker(BigMap)}
          />
          <Route path="/signup" component={withTracker(SignUp)} />
          <Route
            path="/products/my"
            component={withTracker(MyProducts)}
          />
          <Route path="/products/new" component={withTracker(NewProduct)} />
          <Route path="/users" component={withTracker(Users)} />
          <PrivateRoute
            path="/user/edit/:userId"
            component={withTracker(EditProfile)}
          />
          <Route path="/user/:userId" component={withTracker(Profile)} />
          <PrivateRoute
            path="/seller/:shopId/:productId/edit"
            component={withTracker(EditProduct)}
          />
          <Route path="/*" component={withTracker(NotFound)} />
        </Switch>
      </div>
    );
  }
}

export default withTranslation()(App);
